import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class Tab extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dwrui'
    };
    constructor(props) {
        super(props);
        this.offset = 0;
        this.navs = {};
        let activeKey;
        if ('activeKey' in props) {
            activeKey = props.activeKey;
        } else if ('defaultActiveKey' in props) {
            activeKey = props.defaultActiveKey;
        } else {
            activeKey = 0;
        }
        this.state = {
            activeKey
        };
    }
    componentDidMount() {
        this.setActive();
        this.updatePrvNext();
    }
    componentWillReceiveProps(nextProps) {
        if ('activeKey' in nextProps) {
            const { activeKey } = nextProps;
            if (activeKey !== this.state.activeKey) {
                this.setState({
                    activeKey
                });
            }
        }
    }   
    componentDidUpdate(prevProps) {
        this.setActive();
        this.scrollToActive();
        this.updatePrvNext();
    }
    isNextPrevShown(state) {
        if (state) {
            return state.next || state.prev;
        }
        return this.state.next || this.state.prev;
    }
    handleAdd = e => {
        e.preventDefault();
        const props = this.props;
        if(props.onAdd) {
            props.onAdd(e);
        }
    }
    handleChange = (key) => {
        return e => {
            e.preventDefault();
            const props = this.props;
            const { sprefix } = props;
            if(e.target.classList.contains(`${ sprefix }-tab-close`)) {
                if(props.onRemove) {
                    props.onRemove(key);
                }
            } else {
                if(props.onChange) {
                    props.onChange(key);
                } 
            }
        };
    }
    setActive() {
        if(this.navs[this.state.activeKey]) {
            this.sliderDom.style.visibility = 'visible';
            const rect = this.navs[this.state.activeKey].getBoundingClientRect();
            this.sliderDom.style.width = rect.width + 'px';
            this.sliderDom.style.transform = `translateX(${ rect.x - this.navListDom.getBoundingClientRect().x }px)`;
            this.scrollToActive();
        } else {
            this.sliderDom.style.visibility = 'hidden';
        }
    };
    scrollToActive() {
        this.scrollToTab(this.state.activeKey);
    }
    scrollToTab(key) {
        if(this.navs[key]) {
            const navScrollRect = this.navScrollDom.getBoundingClientRect();
            const navRect = this.navs[key].getBoundingClientRect();
            if(navRect.left < navScrollRect.left) {
                this.scroll(this.offset + navScrollRect.left - navRect.left);
            } else if(navRect.left + navRect.width > navScrollRect.left + navScrollRect.width) {
                this.scroll(this.offset -(navRect.left + navRect.width - navScrollRect.left - navScrollRect.width));
            }
        }
    }
    scroll(offset) {
        this.offset = Math.min(0, offset);
        this.navBodyDom.style.transform = `translateX(${ this.offset }px)`;
    }
    updatePrvNext() {
        const { sprefix } = this.props;
        const d = this.navScrollDom.offsetWidth - this.navBodyDom.offsetWidth;
        let prev = false, next = false;
        if(d >= 0) {
            next = false;
            this.offset = 0;
            this.scroll(this.offset);
        } else if(d >= this.offset) {
            this.offset = d;
            this.scroll(d);
            next = false;
        } else {
            next = true;
        }
        if(this.offset < 0) {
            prev = true;
        } else {
            prev = false;
        }
        if(prev) {
            this.prevDom.classList.add(`${ sprefix }-tab-show`);
        } else {
            this.prevDom.classList.remove(`${ sprefix }-tab-show`);
        }
        if(next) {
            this.nextDom.classList.add(`${ sprefix }-tab-show`);
        } else {
            this.nextDom.classList.remove(`${ sprefix }-tab-show`);
        }
    }
    handlePrev = e => {
        e.preventDefault();
        this.scroll(this.offset + this.navWrapDom.offsetWidth);
        this.updatePrvNext();
        // const { activeKey } = this.state;
        // const prevKey = this.getPrvKey(activeKey);
        // console.log('prevKey', prevKey)
        // this.setState({
        //     activeKey: prevKey
        // });
    }
    handleNext = e => {
        e.preventDefault();
        this.scroll(this.offset - this.navWrapDom.offsetWidth);
        this.updatePrvNext();
        // const { activeKey } = this.state;
        // const nextKey = this.getNextKey(activeKey);
        // console.log('nextKey', nextKey)
        // this.setState({
        //     activeKey: nextKey
        // });
    }
    getPrvKey(key) {
        const { children } = this.props;
        let prevKey;
        for(let i = 0; i < children.length; i++) {
            if(children[i].key == key) {
                prevKey = children[i <= 1 ? 0 : i - 1].key;
                break;
            }
        }
        return prevKey;
    }
    getNextKey(key) {
        const { children } = this.props;
        let nextKey;
        for(let i = 0; i < children.length; i++) {
            if(children[i].key == key) {
                nextKey = children[i >= children.length - 2 ? children.length - 1 : i + 1].key;
                break;
            }
        }
        return nextKey;
    }
    render() {
        const { sprefix, children } = this.props;
        const { activeKey } = this.state;
        this.navs = {};
        let marginLeft = 0;
        for(let i = 0; i < children.length; i++) {
            if(children[i].key == activeKey) {
                marginLeft = i;
                break;
            }
        }
        marginLeft = -marginLeft * 100 + '%';
        return (
            <div className={`${sprefix}-tab`}>
                <input className={`${sprefix}-tab-input`} type="hidden" />
                <div className={`${sprefix}-tab-bar`}>
                    <div className={`${sprefix}-tab-add`} onClick={ this.handleAdd }>
                        <span className={`${sprefix}-tab-add-icon`}>
                            <svg viewBox="64 64 896 896" focusable="false" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                <defs>
                                    <style></style>
                                </defs>
                                <path d="M482 152h60q8 0 8 8v704q0 8-8 8h-60q-8 0-8-8V160q0-8 8-8z"></path>
                                <path d="M176 474h672q8 0 8 8v60q0 8-8 8H176q-8 0-8-8v-60q0-8 8-8z"></path>
                            </svg>
                        </span>
                    </div>
                    <div className={`${sprefix}-tab-bar-scrolling`}>
                        <span unselectable="unselectable" className={ `${sprefix}-tab-prev` } onClick={ this.handlePrev } ref={ ref => this.prevDom = ref }>
                            <span className={`${sprefix}-tab-icon-wrap`}>
                                <span className={`${sprefix}-tab-prev-icon`}>
                                    <svg viewBox="64 64 896 896" focusable="false" data-icon="left" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                        <path d="M724 218.3V141c0-6.7-7.7-10.4-12.9-6.3L260.3 486.8a31.86 31.86 0 000 50.3l450.8 352.1c5.3 4.1 12.9.4 12.9-6.3v-77.3c0-4.9-2.3-9.6-6.1-12.6l-360-281 360-281.1c3.8-3 6.1-7.7 6.1-12.6z"></path>
                                    </svg>
                                </span>
                            </span>
                        </span>
                        <span unselectable="unselectable" className={ `${sprefix}-tab-next` } onClick={ this.handleNext } ref={ ref => this.nextDom = ref }>
                            <span className={`${sprefix}-tab-next-icon-wrap`}>
                                <span className={`${sprefix}-tab-next-icon`}>
                                    <svg viewBox="64 64 896 896" focusable="false" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                        <path d="M765.7 486.8L314.9 134.7A7.97 7.97 0 00302 141v77.3c0 4.9 2.3 9.6 6.1 12.6l360 281.1-360 281.1c-3.9 3-6.1 7.7-6.1 12.6V883c0 6.7 7.7 10.4 12.9 6.3l450.8-352.1a31.96 31.96 0 000-50.4z"></path>
                                    </svg>
                                </span>
                            </span>
                        </span>
                        <div className={`${sprefix}-tab-navs`} ref={ ref => this.navWrapDom = ref }>
                            <div className={`${sprefix}-tab-navs-scrolling`} ref={ ref => this.navScrollDom = ref }>
                                <div className={`${sprefix}-tab-navs-body`} ref={ ref => this.navBodyDom = ref }>
                                    <div className={`${sprefix}-tab-navs-list`} ref={ ref => this.navListDom = ref }>
                                        {
                                            React.Children.map(children, (item, index) => {
                                                const cls = [`${ sprefix }-tab-nav`];
                                                if(item.props.activeKey == item.key) {
                                                    cls.push(`${ sprefix }-tab-nav-active`);
                                                }
                                                return (
                                                    <div className={`${ cls.join(' ') }`}
                                                        onClick={ this.handleChange(item.key) }
                                                        ref={ 
                                                            ref => {
                                                                this.navs[item.key] = ref;
                                                            } 
                                                        }
                                                    >
                                                        <div className={ `${ sprefix }-tab-nav-inner` }>
                                                            <span className={ `${ sprefix }-tab-nav-label` }>{ item.props.title }</span>
                                                            <span className={ `${ sprefix }-tab-close` }>
                                                                <svg viewBox="64 64 896 896" focusable="false" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                                                    <path d="M563.8 512l262.5-312.9c4.4-5.2.7-13.1-6.1-13.1h-79.8c-4.7 0-9.2 2.1-12.3 5.7L511.6 449.8 295.1 191.7c-3-3.6-7.5-5.7-12.3-5.7H203c-6.8 0-10.5 7.9-6.1 13.1L459.4 512 196.9 824.9A7.95 7.95 0 00203 838h79.8c4.7 0 9.2-2.1 12.3-5.7l216.5-258.1 216.5 258.1c3 3.6 7.5 5.7 12.3 5.7h79.8c6.8 0 10.5-7.9 6.1-13.1L563.8 512z"></path>
                                                                </svg>
                                                            </span>
                                                        </div>
                                                    </div>
                                                );
                                            })
                                        }
                                    </div>
                                    <div className={`${sprefix}-tab-slider`} ref={ ref => {this.sliderDom = ref;} }></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`${sprefix}-tab-content-list`} style={{ marginLeft }}>
                    {
                        React.Children.map(children, (item, index) => {
                            let newProps = {
                                ...item.props,
                                navKey: item.key,
                            };
                            const TabPanel = React.cloneElement(item, newProps);
                            return TabPanel;
                        })
                    }
                </div>
            </div>
        );
    }
}


