import React, { Component } from 'react';
import PropTypes from 'prop-types';
class Checkbox extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dwrui'
    };
    constructor(props) {
        super(props);
        const checked = props.checked;
        this.state = {
            checked
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        //const {checked} = nextProps;
        if (this.props.checked !== nextProps.checked) {
            this.setState({
                checked: nextProps.checked
            });
        }
    }
    componentDidUpdate() {

    }
    handleChange = (e) => {
        const value = e.target.value;
        const { checked } = this.state;
        const chk = !checked;
        this.setState({
            checked: chk
        });
        if (this.props.onChange) {
            this.props.onChange(e, { ...this.props, checked: chk });
        }

    };
    render() {
        const props = this.props;
        const { className, children, sprefix, ...others } = props;
        const { checked } = this.state;
        return (
            <label className={`${sprefix}-checkbox`}>
                <span className={checked ? `${sprefix}-checkbox-content ${sprefix}-checkbox-checked ${sprefix}-effect-wave` : `${sprefix}-checkbox-content`}>
                    <span className={`${sprefix}-checkbox-inner`}></span>
                    <input className={`${sprefix}-checkbox-input`} type="checkbox" {...others} checked={checked} onChange={this.handleChange} />
                </span>
                <span>{children}</span>
            </label>
        );
    }
}
export default Checkbox;



