import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class UploadImage extends Component {
    static propTypes = {
        placeholder: PropTypes.string,
        name: PropTypes.string,
        disabled: PropTypes.bool
    };
    static defaultProps = {
        sprefix: 'dwrui',
        placeholder: 'Input...',
        fileTitle: '',
        url: '',
        basePath: '',
        name: '',
        disabled: false
    };
    constructor(props) {
        super(props);
        let value = props.value || props.defaultValue;
        this.state = {
            value,
            imageUrl: value
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        if ('value' in nextProps) {
            const { value } = nextProps;
            if (value !== this.state.value) {
                this.setState({
                    value,
                    imageUrl: value
                });
            }
        }
    }
    componentDidUpdate(prevProps) {
    }
    onChange(value, e) {
        if(!('value' in this.props)) {
            this.setState({
                value: value,
                imageUrl: value
            });
        }
        if (this.props.onChange) {
            this.props.onChange(e, { ...this.props, value });
        }
    }
    handleChange = e => {
        e.preventDefault();
        this.handleUploadFiles(e.target.files);
    }
    handleUploadFiles = function(files) {
        const ps = [];
        for(let i = 0; i < files.length; i++) {
            ps.push(this.handleUploadFile(files[i]));
        }
        return Promise.all(ps).then(data => data);
    }
    handleUploadFile(file) {
        let formData = new FormData();
        formData.append('file', file);
        if(this.props.onUpload) {
            this.props.onUpload(formData, file);
        } else {
            fetch(this.props.url, {
                method: 'post',
                // headers: {
                //     contentType: 'application/x-www-form-urlencoded'
                // },
                //headers: {},
                //processData: false,
                //cache: false,
                body: formData
            }).then(response => response.json()).then(rs => {
                const value = this.getImageURL(rs.data);
                this.onChange(value);
            });
        }
        // return ajax({
        //     url: ``,
        //     type: 'post',
        //     data: formData,
        //     contentType: false,
        //     processData: false,
        //     cache: false,
        // }).then(data => data);
    }
    getImageURL(value) {
        if(value !== undefined) {
            return `${ this.props.basePath }/${ value }`;
        }
        return '';
    }
    handleInputChange = e => {
        e.preventDefault();
        this.setState({
            imageUrl: e.target.value
        });
    }
    handleInputKeyPress = e => {
        if (e.nativeEvent.which === 13) {
            e.target.blur();
        }
    }
    handleInputBlur = e => {
        e.preventDefault();
        const { imageUrl } = this.state;
        this.onChange(imageUrl);
    }
    handleClick = e => {
        e.preventDefault();
        this.onChange('');
        this.fileInput.value = '';
    }
    render() {
        const { sprefix, theme, fileTitle, placeholder, name, disabled } = this.props;
        const cls = [`${sprefix}-image-wrap`];
        if(theme === 'dark') {
            cls.push(`${sprefix}-image-wrap-dark`);
        }
        return (
            <div className={ cls.join(' ') }>
                <div className={ `${ sprefix }-image-url-wrap` }>
                    <i className={ `${ sprefix }-image-url-icon` }>
                        <svg viewBox="64 64 896 896" focusable="false" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                            <path d="M574 665.4a8.03 8.03 0 00-11.3 0L446.5 781.6c-53.8 53.8-144.6 59.5-204 0-59.5-59.5-53.8-150.2 0-204l116.2-116.2c3.1-3.1 3.1-8.2 0-11.3l-39.8-39.8a8.03 8.03 0 00-11.3 0L191.4 526.5c-84.6 84.6-84.6 221.5 0 306s221.5 84.6 306 0l116.2-116.2c3.1-3.1 3.1-8.2 0-11.3L574 665.4zm258.6-474c-84.6-84.6-221.5-84.6-306 0L410.3 307.6a8.03 8.03 0 000 11.3l39.7 39.7c3.1 3.1 8.2 3.1 11.3 0l116.2-116.2c53.8-53.8 144.6-59.5 204 0 59.5 59.5 53.8 150.2 0 204L665.3 562.6a8.03 8.03 0 000 11.3l39.8 39.8c3.1 3.1 8.2 3.1 11.3 0l116.2-116.2c84.5-84.6 84.5-221.5 0-306.1zM610.1 372.3a8.03 8.03 0 00-11.3 0L372.3 598.7a8.03 8.03 0 000 11.3l39.6 39.6c3.1 3.1 8.2 3.1 11.3 0l226.4-226.4c3.1-3.1 3.1-8.2 0-11.3l-39.5-39.6z"></path>
                        </svg>
                    </i>
                    <input 
                        className={ `${ sprefix }-image-url` }
                        placeholder={ placeholder }
                        disabled={ disabled }
                        name={ name }
                        value={ this.state.imageUrl || '' }
                        onKeyPress={ this.handleInputKeyPress }
                        onBlur={ this.handleInputBlur }
                        onChange={ this.handleInputChange }
                    />
                </div>
                <div className={ `${ sprefix }-image-add` }>
                    {/* <input className={ `${ sprefix }-image-hidden` } name={ name } type="hidden" value={ this.state.value }/> */}
                    <span className={ `${ sprefix }-image-label` }>{ fileTitle }</span>
                    <div className={ `${ sprefix }-image-img` } style={{ backgroundImage: `url(${ this.state.value })` }}></div>
                    <input ref={ ref => this.fileInput = ref } type="file" className={ `${ sprefix }-image-input` } accept="image/*" onChange={ this.handleChange } disabled={ disabled }/>
                    <div className={ `${ sprefix }-image-oper` }>
                        <span className={ `${ sprefix }-image-edit` } style={{ display: 'none' }}></span>
                        <span className={ `${ sprefix }-image-delete` } onClick={ this.handleClick }></span>
                    </div>
                </div>
            </div>
        );
    }
}


