import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class Swicth extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dwrui'
    };
    constructor(props) {
        super(props);
        let checked = props.checked || props.defaultChecked;
        checked = !!checked;
        this.state = {
            checked
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        //const {checked} = nextProps;
        if (this.props.checked !== nextProps.checked) {
            this.setState({
                checked: nextProps.checked
            });
        }
    }
    componentDidUpdate() {

    }
    handleChange = (e) => {
        const value = e.target.value;
        const { checked } = this.state;
        const chk = !checked;
        if(!('checked' in this.props)) {
            this.setState({
                checked: chk
            });
        }
        if (this.props.onChange) {
            this.props.onChange(e, { ...this.props, checked: chk });
        }
    };
    render() {
        const props = this.props;
        const { className, children, sprefix, checked, defaultChecked, ...others } = props;
        return (
            <label className={ `${ sprefix}-switch-wrap` }>
                <span className={`${sprefix}-switch-inner`}></span>
                <input className={ checked ? `${ sprefix}-switch ${ sprefix}-switch-checked` : `${ sprefix}-switch` }
                    type="checkbox"
                    {...others}
                    checked={this.state.checked}
                    onChange={this.handleChange}
                />
                <span className={ `${ sprefix}-switch-bar` }></span>
                <span className={ `${ sprefix}-switch-label` }>{children}</span>
            </label>
        );
    }
}


