import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class Slider extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        sprefix: PropTypes.string,
        min: PropTypes.number,
        max: PropTypes.number,
        precision: PropTypes.number,
        step: PropTypes.number,
        unit: PropTypes.string,
        name: PropTypes.string,
        disabled: PropTypes.bool
    };
    static defaultProps = {
        sprefix: 'dwrui',
        //value: 0,
        //min: -Infinity,
        //max: Infinity,
        min: -999999999,
        max: 999999999,
        precision: 0,
        step: 1,
        unit: '',
        name: '',
        disabled: false
    };
    constructor(props) {
        super(props);
        let value = props.value || props.defaultValue;
        value = this.resloveValue(value);
        const precision = this.getPrecision(props.precision);
        const disabled = !!props.disabled;
        this.preValue = value;
        const valueText = this.toFixed(value, precision);
        this.state = {
            value,
            valueText,
            precision,
            disabled
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        let newState = {};
        if ('value' in nextProps) {
            const { value } = nextProps;
            if (value !== this.state.value) {
                let v = this.preValue = this.resloveValue(value);
                newState.value = v;
            }
        }
        if ('precision' in nextProps) {
            const { precision } = nextProps;
            if (precision !== this.state.precision) {
                newState.precision = this.getPrecision(precision);
            }
        }
        newState = {
            ...this.state,
            ...newState
        };
        newState.valueText = this.toFixed(newState.value, newState.precision);
        this.setState(newState);
    }
    componentDidUpdate() {
    }
    getPrecision(precision) {
        let p = parseInt(precision, 10);
        return p < 0 ? 0 : p;
    }
    handleChange = (e) => {
        e.preventDefault();
        // const value = e.target.value;
        // if (this.props.onChange) {
        //     this.props.onChange(e, { ...this.props, value });
        // } else {
        //     this.setState({
        //         value
        //     });
        // }
        this.onChange(Number(e.target.value), e);
    };
    onChange(value, e) {
        this.preValue = value;
        //if (!('value' in this.props)) {
            const valueText = this.toFixed(value, this.state.precision);
            this.setState({
                valueText,
                value
            });
        //}
        if (this.props.onChange) {
            this.props.onChange(e, { ...this.props, value });
        }
    }
    resloveValue(v) {
        const { min, max } = this.props;
        let value = this.trim(v), newValue;
        if (value === "") {
            newValue = this.preValue;
        } else if (value !== undefined) {
            if (isNaN(value)) {
                value = this.preValue;
            }
            if (value < min) {
                value = min;
            }
            if (value > max) {
                value = max;
            }
            newValue = value;
        }
        return Number(newValue);
    }
    handleTextChange = e => {
        e.preventDefault();
        this.setState({
            valueText: e.target.value
        })
    }
    handleTextKeyPress = e => {
        if (e.nativeEvent.which === 13) {
            e.target.blur();
        }
    }
    handleTextBlur = e => {
        e.preventDefault();
        const { valueText } = this.state;
        let v = this.resloveValue(valueText);
        this.onChange(v, e);
    }
    trim(v) {
        if(v !== undefined && v !== null) {
            return v.toString().replace(/(^\s*)|(\s*$)/g, "");
        }
        return 0;
    }
    toFixed(v, precision) {
        if(!isNaN(v)) {
            return Number(v).toFixed(precision); 
        }
        return v;
    }
    render() {
        const props = this.props;
        const { className, theme, children, sprefix, min, max, ...others } = props;
        const scale = (this.state.value - min) / (max - min) * 100;
        const cls = [`${sprefix}-slider`];
        if(theme === 'dark') {
            cls.push(`${sprefix}-slider-dark`);
        }
        return (
            <div className={ cls.join(' ') }>
                <div className={ `${ sprefix}-slider-thumb` }>
                    <input className={ `${ sprefix}-slider-range` }
                        { ...others }
                        min={ min }
                        max={ max }
                        type="range"
                        value={ this.state.value }
                        onChange={ this.handleChange }
                        ref={ ref => this.input = ref }
                        style={{ '--slider-scale': scale + '%' }}
                    />
                </div>
                <div className={ `${ sprefix}-slider-content` }>
                    <ul>
                        <li>
                            <div className={ `${ sprefix}-slider-min` }>{ min }</div>
                        </li>
                        <li>
                            <input className={ `${ sprefix}-slider-value` }
                                type="text"
                                value={ this.state.valueText }
                                onKeyPress={this.handleTextKeyPress}
                                onBlur={this.handleTextBlur}
                                onChange={this.handleTextChange}
                            />
                        </li>
                        <li>
                            <div className={ `${ sprefix}-slider-max` }>{ max }</div>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}


