import DaoUtil from './DaoUtil';
const Ajax = async function (url, option = {}) {
    // return fetch(url, {
    //     // body: JSON.stringify(data), // must match 'Content-Type' header
    //     cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
    //     //credentials: 'same-origin', // include, same-origin, *omit
    //     headers: {
    //       //'user-agent': 'Mozilla/4.0 MDN Example',
    //       'content-type': 'application/json',
    //       'Authorization': ('Bearer ' + localStorage.getItem('token')) || ''
    //     },
    //     method: 'GET', // *GET, POST, PUT, DELETE, etc.
    //     //mode: 'cors', // no-cors, cors, *same-origin
    //     //redirect: 'follow', // manual, *follow, error
    //     //referrer: 'no-referrer', // *client, no-referrer
    //     ...option
    // }).then(response => response.json());
    if (url.indexOf(`platform/login`) !== -1) {
        const { userName, password } = JSON.parse(option.body || '{}');
        const usersData = await DaoUtil.get('users', {
            name: userName,
            password
        });
        return usersData;
    } else if (url.indexOf(`screen/getScreen`) !== -1) {
        const path = url.split('/');
        const screenID = Number(path[path.length - 1]);
        const screenData = await DaoUtil.get('screen', {
            id: screenID
        });
        const configData = await DaoUtil.get('config', {
            screenId: screenID
        });
        const componentData = await DaoUtil.getList('component', {
            screenId: screenID
        });
        const data = {
            ...screenData.data,
            config: configData.data,
            layer: [{
                components: componentData.data.sort((a, b) => {
                    return b.order - a.order;
                })
            }]
        };
        const rs = await Promise.resolve({
            code: 0,
            data
        });
        // const rs = await new Promise(resolve => {
        //     resolve({
        //         code: 0,
        //         data
        //     });
        // });
        return rs;
    } else if (url.indexOf(`screen/add`) !== -1) {
        const params = JSON.parse(option.body);
        const screenData = await DaoUtil.save('screen', params);
        const id = screenData.data;
        const configData = await DaoUtil.save('config', {
            screenId: id
        });
        return configData;
    } else if (url.indexOf(`screen/getList`) !== -1) {
        const screenData = await DaoUtil.getList('screen');
        return screenData;
    } else if (url.indexOf(`screen/del`) !== -1) {
        const params = JSON.parse(option.body);
        const screenData = await DaoUtil.delete('screen', Number(params.id));
        return screenData;
    } else if (url.indexOf('component/add') !== -1) {
        const params = JSON.parse(option.body);
        params.screenId = Number(params.screenId);
        const componentData = await DaoUtil.save('component', params);
        params.id = componentData.data;
        const rs = await Promise.resolve({
            code: 0,
            data: params
        });
        return rs;
    } else if (url.indexOf('component/update') !== -1) {
        const { type, param } = JSON.parse(option.body);
        let componentData;
        //if (type === 'attr') {
            for (let i = 0; i < param.length; i++) {
                componentData = await DaoUtil.update('component', param[i]);
            }
        //}
        return componentData;
    } else if (url.indexOf('component/del') !== -1) {
        const { id } = JSON.parse(option.body);
        let configData;
        for (let i = 0; i < id.length; i++) {
            configData = await DaoUtil.delete('component', id[i]);
        }
        return configData;
    } else if (url.indexOf('config/update') !== -1) {
        const params = JSON.parse(option.body);
        const configData = await DaoUtil.update('config', params);
        return configData;
    } else if (url.indexOf('upload/image') !== -1) {
        const fr = new FileReader();
        let rs = await new Promise((resolve, reject) => {
            fr.onload = e => {
                console.log(e);
                resolve({
                    code: 0,
                    data: e.target.result
                });
            }
            fr.readAsDataURL(option.body);
        });
        // const rs = {
        //     code: 0,
        //     data: window.URL.createObjectURL(option.body)
        // }
        return rs;
    } else {
        console.log(url, option);
        const data = await new Promise((resolve, reject) => {
            resolve({
                code: 0,
                data: []
            });
        });
        return data;
    }

}
export default Ajax;



