import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class Loading extends Component {
    static propTypes = {
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dwrui'
    };
    constructor(props) {
        super(props);
        let visible = props.visible || props.defaultVisible;
        visible = !!visible;
        this.state = {
            visible
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        if('visible' in nextProps) {
            let visible = nextProps.visible;
            visible = !!visible;
            this.setState({
                visible
            });
        }
    }
    componentDidUpdate() {
    }
    render() {
        const { sprefix, children } = this.props;
        const { visible } = this.state;
        const cls = [`${ sprefix}-loading`];
        if(visible) {
            cls.push(`${ sprefix}-loading-show`);
        }
        return (
            <div className={ cls.join(' ') }>
                <div className={ `${ sprefix}-loading-inner` }>
                    <svg width="50px" height="50px">
                        <circle cx="25" cy="25" r="20" fill="transparent" strokeWidth="3" strokeDasharray="31.415, 31.415" stroke="#02bcfe" strokeLinecap="round" transform="rotate(157.094 25 25)">
                            <animateTransform attributeName="transform" type="rotate" values="0, 25 25;360, 25 25" dur="1.5s" repeatCount="indefinite"></animateTransform>
                            <animate attributeName="stroke" values="#02bcfe;#3be6cb;#02bcfe" dur="3s" repeatCount="indefinite"></animate>
                        </circle>
                        <circle cx="25" cy="25" r="10" fill="transparent" strokeWidth="3" strokeDasharray="15.7, 15.7" stroke="#3be6cb" strokeLinecap="round" transform="rotate(202.906 25 25)">
                            <animateTransform attributeName="transform" type="rotate" values="360, 25 25;0, 25 25" dur="1.5s" repeatCount="indefinite"></animateTransform>
                            <animate attributeName="stroke" values="#3be6cb;#02bcfe;#3be6cb" dur="3s" repeatCount="indefinite"></animate>
                        </circle>
                    </svg>
                    <div className={ `${ sprefix}-loading-text` }>{ children }</div>
                </div>
            </div>
        );
    }
}


