import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
class Resizable extends Component {
    static propTypes = {
        //onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui',
        handles: 'all'
        //onChange: () => { }
    };
    constructor(props) {
        super(props);
        this.height = null;
        this.width = null;
        this.heightIncrement = 0;
        this.widthIncrement = 0;
        this.minHeight = 5;
        this.minWidth = 5;
        this.maxHeight = 10000;
        this.maxWidth = 10000;
        this.minX = 0;
        this.minY = 0;
        this.handles = 'all';
        this.multiDirectional = false;
        this.positions = {
            n: "north",
            s: "south",
            e: "east",
            w: "west",
            se: "southeast",
            sw: "southwest",
            nw: "northwest",
            ne: "northeast"
        }
        this.pos = null;
        this.preserveRatio = false;
        let rect = props.rect || props.defaultRect || [0, 0, 0, 0];
        this.state = {
            rect
        }
    }
    componentDidMount() {
        const { handles } = this.props;
        if (!handles) {
            this.handles = "s,e,se";
            if (this.multiDirectional) {
                this.handles += ",n,w";
            }
        }
        if (handles == "all") {
            this.handles = "n s e w ne nw se sw";
        }
        this.el = ReactDOM.findDOMNode(this);
        let o = this.handles.split(/\s*?[,;]\s*?| /);
        for (let j = 0, l = o.length; j < l; j++) {
            if (o[j] && this.positions[o[j]]) {
                var n = this.positions[o[j]];
                this[n] = this.createHandle(n);
            }
        }
        this.corner = this.southeast;
        if (this.handles.indexOf("n") != -1 || this.handles.indexOf("w") != -1) {
            this.updateBox = true;
        }
        if (navigator.userAgent.toLowerCase().indexOf("msie") > -1) {
            this.el.style.zoom = 1;
        }
    }
    componentWillReceiveProps(nextProps) {
        if ('rect' in nextProps) {
            const { rect } = nextProps;
            if (rect !== this.state.rect) {
                this.setState({
                    rect
                });
            }
        }
    }
    componentDidUpdate() {

    }
    componentWillUnmount() {

    }
    getDom(el) {
        if (el === undefined) {
            return null;
        }
        return el.get ? el.get(0) : (typeof el == "string" ? document.getElementById(el) : el);
    }
    getStyle(el, style) {
        let dom = this.getDom(el);
        if (dom.currentStyle) {
            return dom.currentStyle[style];
        } else {
            return window.getComputedStyle(dom, null)[style];
        }
    }
    getSpace() {
        if (this.props.getSpace) {
            return this.props.getSpace();
        }
        return 10;
    }
    getScale() {
        if (this.props.getScale) {
            return this.props.getScale();
        }
        return 1;
    }
    constrain(b, c, a, d) {
        if (b - c < a) {
            c = b - a;
        } else {
            if (b - c > d) {
                c = b - d;
            }
        }
        return c;
    }
    snap(c, e, b) {
        if (!e || !c) {
            return c
        }
        let d = c, a = c % e;
        if (a > 0) {
            if (a > (e / 2)) {
                d = c + (e - a)
            } else {
                d = c - a
            }
        }
        return Math.max(b, d);
    }
    getPosByDom(dom) {
        let xy = dom.style.transform.split(/[(|,|)]/g);
        return {
            left: parseFloat(xy[1], 10),
            top: parseFloat(xy[2], 10)
        };
    }
    setPos(dom, pos) {
        dom.style.transform = `translate(${pos[0]}px, ${pos[1]}px)`;
        //dom.style.left = pos[0] + 'px';
        //dom.style.top = pos[1] + 'px';
    }
    getRect(dom) {
        let { left, top } = this.getPosByDom(dom);
        return {
            left,
            top,
            width: dom.offsetWidth,
            height: dom.offsetHeight
        };
    }
    getStyle(el, style) {
        let dom = this.getDom(el);
        if (dom.currentStyle) {
            return dom.currentStyle[style];
        } else {
            return window.getComputedStyle(dom, null)[style];
        }
    }
    addStyles(e, sides, styles) {
        var val = 0, v, w, el = e;
        for (var i = 0, len = sides.length; i < len; i++) {
            v = this.getStyle(el, styles[sides.charAt(i)])
            if (v) {
                w = parseInt(v, 10);
                if (w) {
                    val += w;
                }
            }
        }
        return val;
    }
    getBorderWidth(el, side) {
        return this.addStyles(el, side, {
            l: "border-left-width",
            r: "border-right-width",
            t: "border-top-width",
            b: "border-bottom-width"
        });
    }
    getPadding(el, side) {
        return this.addStyles(el, side, {
            l: "padding-left",
            r: "padding-right",
            t: "padding-top",
            b: "padding-bottom"
        });
    }
    onMouseDown(e) {
        if (this.props.onMouseDown) {
            this.props.onMouseDown(e, this);
        }
    }
    onMouseMove(e) {
        if (!('rect' in this.props)) {
            this.setState({
                rect: this.endBox
            });
        }
        if (this.props.onMouseMove) {
            this.props.onMouseMove(e, this);
        }
    }
    onMouseUp(e) {
        if (this.props.onMouseUp) {
            this.props.onMouseUp(e, this);
        }
    }
    handleMouseDown = (e) => {
        if (e.preventDefault) {
            e.preventDefault();
        } else {
            e.returnValue = false;
        }
        if (e.stopPropagation) {
            e.stopPropagation();
        } else {
            e.cancelBubble = true;
        }
        this.current = e.target;
        this.pos = this.current.getAttribute('pos');
        if (!Resizable.overlay) {
            Resizable.overlay = document.createElement("div");
            Resizable.overlay.className = `${this.props.sprefix}-resizable-overlay`;
            Resizable.overlay.innerHtml = "&#160;";
            document.body.appendChild(Resizable.overlay);
            Resizable.overlay.style['-moz-user-select'] = 'none';
            Resizable.overlay.style['-khtml-user-select'] = 'none';
            Resizable.overlay.style['user-select'] = 'none';
            Resizable.overlay.style['-webkit-user-select'] = 'ignore';
            Resizable.overlay.style.display = 'none';
        }
        Resizable.overlay.addEventListener('mousemove', this.handleMouseMove, false);
        Resizable.overlay.addEventListener('mouseup', this.handleMouseUp, false);
        Resizable.overlay.style.display = 'block';
        this.resizing = true;
        //let { left, top } = this.getPosByDom(this.el);
        this.startBox = this.getRect(this.el);
        this.endBox = this.startBox;
        this.startPoint = [e.pageX, e.pageY];
        this.increaseXY = [0, 0];
        this.onMouseDown(e);
    }
    handleMouseMove = (e) => {
        if (e.preventDefault) {
            e.preventDefault();
        } else {
            e.returnValue = false;
        }
        if (e.stopPropagation) {
            e.stopPropagation();
        } else {
            e.cancelBubble = true;
        }
        this.increaseXY = [e.pageX - this.startPoint[0], e.pageY - this.startPoint[1]];
        this.endBox = this.resizeMove(this.el, this.pos, this.increaseXY, {
            startBox: this.startBox
        });
        this.onMouseMove(e);
    }
    handleMouseUp = (e) => {
        if (e.preventDefault) {
            e.preventDefault();
        } else {
            e.returnValue = false;
        }
        if (e.stopPropagation) {
            e.stopPropagation();
        } else {
            e.cancelBubble = true;
        }
        this.resizing = false;
        Resizable.overlay.removeEventListener('mousemove', this.handleMouseMove);
        Resizable.overlay.removeEventListener('mouseup', this.handleMouseUp);
        Resizable.overlay.style.display = 'none';
        this.onMouseUp(e);
    }
    resizeMove(dom, pos, increaseXY, opt) {
        if (this.resizing && pos) {
            let t = opt.curSize || opt.startBox,
                l = t.left,
                k = t.top,
                c = l,
                b = k,
                m = t.width,
                u = t.height,
                d = m,
                o = u,
                n = opt.minWidth !== undefined ? opt.minWidth : this.minWidth,
                A = opt.minHeight !== undefined ? opt.minHeight : this.minHeight,
                s = opt.maxWidth !== undefined ? opt.maxWidth : this.maxWidth,
                D = opt.maxHeight !== undefined ? opt.maxHeight : this.maxHeight,
                i = opt.widthIncrement !== undefined ? opt.widthIncrement : this.widthIncrement,
                a = opt.heightIncrement !== undefined ? opt.heightIncrement : this.heightIncrement,
                //B = [e.pageX, e.pageY],
                //r = -(this.startPoint[0] - Math.max(this.minX, B[0])),
                //p = -(this.startPoint[1] - Math.max(this.minY, B[1])),
                scale = this.getScale(),
                r = Math.round(increaseXY[0] / scale / this.getSpace()) * this.getSpace() + (opt && opt.increment ? opt.increment[0] : 0),
                p = Math.round(increaseXY[1] / scale / this.getSpace()) * this.getSpace() + (opt && opt.increment ? opt.increment[1] : 0),
                j = pos, E, g;
            switch (j) {
                case "east":
                    m += r;
                    m = Math.min(Math.max(n, m), s);
                    break;
                case "south":
                    u += p;
                    u = Math.min(Math.max(A, u), D);
                    break;
                case "southeast":
                    m += r;
                    u += p;
                    m = Math.min(Math.max(n, m), s);
                    u = Math.min(Math.max(A, u), D);
                    break;
                case "north":
                    p = this.constrain(u, p, A, D);
                    k += p;
                    u -= p;
                    break;
                case "west":
                    r = this.constrain(m, r, n, s);
                    l += r;
                    m -= r;
                    break;
                case "northeast":
                    m += r;
                    m = Math.min(Math.max(n, m), s);
                    p = this.constrain(u, p, A, D);
                    k += p;
                    u -= p;
                    break;
                case "northwest":
                    r = this.constrain(m, r, n, s);
                    p = this.constrain(u, p, A, D);
                    k += p;
                    u -= p;
                    l += r;
                    m -= r;
                    break;
                case "southwest":
                    r = this.constrain(m, r, n, s);
                    u += p;
                    u = Math.min(Math.max(A, u), D);
                    l += r;
                    m -= r;
                    break
            }
            var q = this.snap(m, i, n);
            var C = this.snap(u, a, A);
            if (q != m || C != u) {
                switch (j) {
                    case "northeast":
                        k -= C - u;
                        break;
                    case "north":
                        k -= C - u;
                        break;
                    case "southwest":
                        l -= q - m;
                        break;
                    case "west":
                        l -= q - m;
                        break;
                    case "northwest":
                        l -= q - m;
                        k -= C - u;
                        break
                }
                m = q;
                u = C
            }
            if (this.preserveRatio) {
                switch (j) {
                    case "southeast":
                    case "east":
                        u = o * (m / d);
                        u = Math.min(Math.max(A, u), D);
                        m = d * (u / o);
                        break;
                    case "south":
                        m = d * (u / o);
                        m = Math.min(Math.max(n, m), s);
                        u = o * (m / d);
                        break;
                    case "northeast":
                        m = d * (u / o);
                        m = Math.min(Math.max(n, m), s);
                        u = o * (m / d);
                        break;
                    case "north":
                        E = m;
                        m = d * (u / o);
                        m = Math.min(Math.max(n, m), s);
                        u = o * (m / d);
                        l += (E - m) / 2;
                        break;
                    case "southwest":
                        u = o * (m / d);
                        u = Math.min(Math.max(A, u), D);
                        E = m;
                        m = d * (u / o);
                        l += E - m;
                        break;
                    case "west":
                        g = u;
                        u = o * (m / d);
                        u = Math.min(Math.max(A, u), D);
                        k += (g - u) / 2;
                        E = m;
                        m = d * (u / o);
                        l += E - m;
                        break;
                    case "northwest":
                        E = m;
                        g = u;
                        u = o * (m / d);
                        u = Math.min(Math.max(A, u), D);
                        m = d * (u / o);
                        k += g - u;
                        l += E - m;
                        break
                }
            }
            let width;
            let height;
            //dom.style.width = (m - this.getBorderWidth(dom, 'lr') - this.getPadding(dom, 'lr')) + 'px';
            //dom.style.height = (u - this.getBorderWidth(dom, 'tb') - this.getPadding(dom, 'tb')) + 'px';
            //dom.style.transform = `translate(${l}px, ${k}px)`;
            if (!dom) {
                width = m;
                height = u;
            } else {
                width = (m - this.getBorderWidth(dom, 'lr') - this.getPadding(dom, 'lr'));
                height = (u - this.getBorderWidth(dom, 'tb') - this.getPadding(dom, 'tb'));
            }
            return {
                left: l,
                top: k,
                width,
                height
            };
        }
        return opt.startBox;
    }
    createHandle(pos) {
        const { sprefix } = this.props;
        let hl = document.createElement('div');
        hl.className = `${sprefix}-resizable-handle ${sprefix}-resizable-handle-${pos}`;
        hl.style['-moz-user-select'] = 'none';
        hl.style['-khtml-user-select'] = 'none';
        hl.style['user-select'] = 'none';
        hl.style['-webkit-user-select'] = 'ignore';
        hl.setAttribute('pos', pos);
        this.el.appendChild(hl);
        hl.addEventListener('mousedown', this.handleMouseDown, false);
    }
    render() {
        const { rect } = this.state;
        const { width, height, left, top } = rect;
        // const { sprefix } = this.props;
        let w = parseFloat(width, 10) || 0;
        let h = parseFloat(height, 10) || 0;
        let l = parseFloat(left, 10) || 0;
        let t = parseFloat(top, 10) || 0;
        const style = {
            ...this.props.children.props.style,
            width: `${ w }px`,
            height: `${ h }px`,
            transform: `translate(${ l }px, ${ t }px)`
        };
        return (
            React.cloneElement(React.Children.only(this.props.children), {
                style
            })
        );
    }
}
export default Resizable;



