import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Numberic extends Component {
    static propTypes = {
        //defaultValue: PropTypes.PropTypes.string,
        //value: PropTypes.PropTypes.string,
        placeholder: PropTypes.string,
        min: PropTypes.number,
        max: PropTypes.number,
        precision: PropTypes.number,
        step: PropTypes.number,
        unit: PropTypes.string,
        name: PropTypes.string,
        disabled: PropTypes.bool
    };
    static defaultProps = {
        sprefix: 'dwrui',
        placeholder: 'Input...',
        //value: 0,
        //min: -Infinity,
        //max: Infinity,
        min: -999999999,
        max: 999999999,
        precision: 0,
        step: 1,
        unit: '',
        name: '',
        disabled: false
    };
    constructor(props) {
        super(props);
        let value = props.value || props.defaultValue;
        value = this.resloveValue(value);
        const precision = this.getPrecision(props.precision);
        // const disabled = !!props.disabled;
        this.preValue = value;
        const valueText = this.toFixed(value, precision);
        this.state = {
            value,
            valueText,
            precision
            // disabled
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        let newState = {};
        if ('value' in nextProps) {
            const { value } = nextProps;
            if (value !== this.state.value) {
                let v = this.preValue = this.resloveValue(value);
                newState.value = v;
            }
        }
        if ('precision' in nextProps) {
            const { precision } = nextProps;
            if (precision !== this.state.precision) {
                newState.precision = this.getPrecision(precision);
            }
        }
        newState = {
            ...this.state,
            ...newState
        };
        newState.valueText = this.toFixed(newState.value, newState.precision);
        this.setState(newState);
    }
    componentDidUpdate(prevProps) {
    }
    getPrecision(precision) {
        let p = parseInt(precision, 10);
        return p < 0 ? 0 : p;
    }
    onAdd = (e) => {
        e.preventDefault();
        if (this.props.disabled) {
            return;
        }
        const { value } = this.state;
        const { step } = this.props;
        const v = value + Number(step);
        this.onChange(v, e);
    }
    onReduce = (e) => {
        e.preventDefault();
        if (this.props.disabled) {
            return;
        }
        const { value } = this.state;
        const { step } = this.props;
        const v = value - Number(step);
        this.onChange(v, e);
    }
    onMouseDown = (e) => {
        e.preventDefault();
        const { value } = this.state;
        if (this.props.disabled) {
            return;
        }
        this.distance = 0;
        this.isMove = false;
        this.onMouseDownValue = Number(value);
        this.prevPointer = [e.clientX, e.clientY];
        document.addEventListener('mousemove', this.onMouseMove, false);
        document.addEventListener('mouseup', this.onMouseUp, false);
    }
    onMouseMove = (e) => {
        e.preventDefault();
        const { step, min, max } = this.props;
        //let currentValue = value;
        this.pointer = [e.clientX, e.clientY];
        this.distance += (this.pointer[0] - this.prevPointer[0]) - (this.pointer[1] - this.prevPointer[1]);
        if (this.distance) {
            let value = this.onMouseDownValue + (this.distance / (e.shiftKey ? 5 : 5)) * Number(step);
            // value = Math.floor(Math.min(max, Math.max(min, value)));
            value = this.toFixed(Math.min(max, Math.max(min, value)), this.state.precision);
            this.onChange(value, e);
            this.isMove = true;
            this.prevPointer = [e.clientX, e.clientY];
        }
        
    }
    onMouseUp = (e) => {
        e.preventDefault();
        document.removeEventListener('mousemove', this.onMouseMove, false);
        document.removeEventListener('mouseup', this.onMouseUp, false);
        this.isMove = false;
        if (this.isMove && this.onMouseDownValue != this.state.value) {

        }
    }
    resloveValue(v) {
        const { min, max } = this.props;
        let value = this.trim(v), newValue;
        if (value === "") {
            newValue = this.preValue;
        } else if (value !== undefined) {
            if (isNaN(value)) {
                value = this.preValue;
            }
            if (value < min) {
                value = min;
            }
            if (value > max) {
                value = max;
            }
            newValue = value;
        }
        return Number(newValue);
    }
    onChange(value, e) {
        this.preValue = value;
        // if (!('value' in this.props)) {
            const valueText = this.toFixed(value, this.state.precision);
            this.setState({
                valueText,
                value: Number(value)
            });
        // }
        if (this.props.onChange) {
            this.props.onChange(e, { ...this.props, value });
        }
    }
    handleChange = e => {
        e.preventDefault();
        this.setState({
            valueText: e.target.value
        })
    }
    handleKeyPress = e => {
        if (e.nativeEvent.which === 13) {
            e.target.blur();
        }
    }
    handleBlur = e => {
        e.preventDefault();
        const { valueText } = this.state;
        let v = this.resloveValue(valueText);
        this.onChange(v, e);
    }
    trim(v) {
        if(v !== undefined && v !== null) {
            return v.toString().replace(/(^\s*)|(\s*$)/g, "");
        }
        return 0;
    }
    toFixed(v, precision) {
        if(!isNaN(v)) {
            return Number(v).toFixed(precision); 
        }
        return v;
    }
    render() {
        const { sprefix, theme, name, disabled } = this.props;
        const cls = [`${sprefix}-number`];
        if(theme === 'dark') {
            cls.push(`${sprefix}-number-dark`);
        }
        return (
            <div className={ cls.join(' ') }>
                <input
                    className={`${sprefix}-number-input`}
                    type="text"
                    name={name}
                    placeholder={this.props.placeholder}
                    value={this.state.valueText}
                    onKeyPress={this.handleKeyPress}
                    onBlur={this.handleBlur}
                    onChange={this.handleChange}
                    disabled={ disabled }
                />
                <span className={`${sprefix}-number-unit`}>{this.props.unit}</span>
                <div className={`${sprefix}-number-oper-wrap`} onMouseDown={this.onMouseDown}>
                    <span className={`${sprefix}-number-oper ${sprefix}-number-add`}
                        onClick={this.onAdd}
                    ></span>
                    <span className={`${sprefix}-number-oper ${sprefix}-number-reduce`}
                        onClick={this.onReduce}
                    ></span>
                </div>
            </div>
        );
    }
}
export default Numberic;



