import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Dropdown from "../Dropdown";
import ColorPanel from "./ColorPanel";

class ColorPicker extends Component {
    static propTypes = {
        defaultValue: PropTypes.string,
        value: PropTypes.string,
        //type: PropTypes.string,
        cls: PropTypes.string,
        onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui'
    };
    constructor(props) {
        super(props);
        let value = props.value || props.defaultValue;
        this.state = {
            visible: false,
            value
        }
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        if('value' in nextProps) {
            const { value } = nextProps;
            if(value !== this.state.value) {
                this.setState({
                    value
                });
            }
        }
        if('visible' in nextProps) {
            const { visible } = nextProps;
            if(visible !== this.state.value) {
                this.setState({
                    visible
                });
            }
        }
    }
    componentDidUpdate() {
    }
    onPopupVisibleChange = (visible) => {
        this.setState({
            visible
        });
    };
    onChange = v => {
        const props = this.props;
        const { value } = this.state;
        if(v !== value) {
            if(!('value' in props)) {
                this.setState({
                    value: v
                });
                //this.onPopupVisibleChange(false);
            }
            if(props.onChange) {
                let ret = props.onChange(null, { ...props, value: v});
                //if(!ret) {
                    //this.onPopupVisibleChange(false);
                //}
            } else {
                this.setState({
                    value: v
                });
            }
        }
    }
    render() {
        const { value, visible } = this.state;
        const { sprefix, cls, type } = this.props;
        return (
            <Dropdown
                sprefix={ `${ sprefix }-colorPicker` }
                menu={
                    <ColorPanel
                        value={ value }
                        type={ type }
                        cls={ cls || `${ sprefix }-colorPicker-dark` }
                        onChange={ this.onChange }
                    ></ColorPanel>
                }
                width={ 'auto' }
                visible={ visible }
                onPopupVisibleChange={ this.onPopupVisibleChange }
                trigger={ "click" }
            >
                <div className={ `${ sprefix }-colorPicker-wrap ${ cls }` }>
                    <div className={ `${ sprefix }-colorPicker-content-bg` }>
                        <div className={ `${ sprefix }-colorPicker-content` } style={{ background: value }}></div>
                    </div>
                    <div className={ `${ sprefix }-colorPicker-input-wrap` }>
                        <input className={ `${ sprefix }-colorPicker-input` } type="text"/>
                    </div>
                    <i className={ `${ sprefix }-colorPicker-arrow-icon` }></i>
                </div>
            </Dropdown>
        );
    }
}
export default ColorPicker;



