import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class TextAlign extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui',
    };
    constructor(props) {
        super(props);
        let value = props.value || props.defaultValue;
        this.state = {
            value
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        if ('value' in nextProps) {
            const { value } = nextProps;
            if (value !== this.state.value) {
                this.setState({
                    value
                });
            }
        }
    }
    componentDidUpdate() {

    }
    handleChange = (v) => {
        return e => {
            const value = v;
            if (!('value' in this.props)) {
                this.setState({
                    value
                });
            }
            if (this.props.onChange) {
                this.props.onChange(e, { ...this.props, value });
            }
        };
    };
    getItemsCls(item) {
        const { sprefix } = this.props;
        const { value } = this.state;
        if(item === value) {
            return `${ sprefix }-textAlign-item ${ sprefix }-textAlign-item-active`;
        }
        return `${ sprefix }-textAlign-item`;
    }
    render() {
        const props = this.props;
        const { sprefix, name, theme } = props;
        const cls = [`${ sprefix }-textAlign`];
        if(theme === 'dark') {
            cls.push(`${ sprefix }-textAlign-dark`);
        }
        return (
            <div className={ cls.join(' ') }>
                <input className={ `${ sprefix }-textAlign-input` } name={ name } value={ this.state.value } type="hidden"/>
                <div className={ this.getItemsCls('left') } onClick={ this.handleChange('left') }>
                    <i className={ `${ sprefix }-textAlign-icon` }>
                        <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 7V5h20v2H2zm0 4V9h14v2H2zm0 4v-2h20v2H2zm0 4v-2h14v2H2z" fillRule="evenodd"></path>
                        </svg>
                    </i>
                </div>
                <div className={ this.getItemsCls('center') } onClick={ this.handleChange('center') }>
                    <i className={ `${ sprefix }-textAlign-icon` }>
                        <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 7V5h20v2H2zm3 4V9h14v2H5zm-3 4v-2h20v2H2zm3 4v-2h14v2H5z" fillRule="evenodd"></path>
                        </svg>
                    </i>
                </div>
                <div className={ this.getItemsCls('right') } onClick={ this.handleChange('right') }>
                    <i className={ `${ sprefix }-textAlign-icon` }>
                        <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 7V5h20v2H2zm6 4V9h14v2H8zm-6 4v-2h20v2H2zm6 4v-2h14v2H8z" fillRule="evenodd"></path>
                        </svg>
                    </i>
                </div>
                <div className={ this.getItemsCls('justify') } onClick={ this.handleChange('justify') }>
                    <i className={ `${ sprefix }-textAlign-icon` }>
                        <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                            <path d="M2 7V5h20v2H2zm0 4V9h20v2H2zm0 4v-2h20v2H2zm0 4v-2h20v2H2z" fillRule="evenodd"></path>
                        </svg>
                    </i>
                </div>
            </div>
        );
    }
}


