import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class FontStyle extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui',
    };
    constructor(props) {
        super(props);
        let value = props.value || props.defaultValue;
        this.state = {
            value
        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        if ('value' in nextProps) {
            const { value } = nextProps;
            if (value !== this.state.value) {
                this.setState({
                    value
                });
            }
        }
    }
    componentDidUpdate() {

    }
    handleChange = (key, v) => {
        return e => {
            const { value } = this.state;
            if(key in value && value[key] === v) {
                delete value[key];
            } else {
                value[key] = v;
            }
            if (!('value' in this.props)) {
                this.setState({
                    value
                });
            }
            if (this.props.onChange) {
                this.props.onChange(e, { ...this.props, value });
            }
        };
    };
    getItemsCls(key, v) {
        const { sprefix } = this.props;
        const { value } = this.state;
        if(v === value[key]) {
            return `${ sprefix }-fontStyle-item ${ sprefix }-fontStyle-item-active`;
        }
        return `${ sprefix }-fontStyle-item`;
    }
    render() {
        const props = this.props;
        const { sprefix, name, theme } = props;
        const cls = [`${ sprefix }-fontStyle`];
        if(theme === 'dark') {
            cls.push(`${ sprefix }-fontStyle-dark`);
        }
        return (
            <div className={ cls.join(' ') }>
                <input className={ `${ sprefix }-fontStyle-input` } name={ name } value={ this.state.value } type="hidden"/>
                <div className={ `${ sprefix }-fontStyle` }>
                    <input className={ `${ sprefix }-fontStyle-input` } type="hidden"/>
                    <div className={ this.getItemsCls('fontWeight', 'bold') } onClick={ this.handleChange('fontWeight', 'bold') }>
                        <i className={ `${ sprefix }-fontStyle-icon` }>
                            <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                <path d="M15.406 11.643C16.945 12.496 18 14.117 18 16a5 5 0 0 1-5 5H6V3h6a5 5 0 0 1 5 5 4.97 4.97 0 0 1-1.594 3.643zM8 13v6h5c1.654 0 3-1.346 3-3s-1.346-3-3-3H8zm0-8v6h4c1.654 0 3-1.346 3-3s-1.346-3-3-3H8z" fillRule="evenodd"></path>
                            </svg>
                        </i>
                    </div>
                    <div className={ this.getItemsCls('fontStyle', 'italic') } onClick={ this.handleChange('fontStyle', 'italic') }>
                        <i className={ `${ sprefix }-fontStyle-icon` }>
                            <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                <path d="M9 4V3h10v1h-2.998C15.448 4 15 4.448 15 5.002l-4 13.996c0 .554.448 1.002 1.002 1.002H15v1H5v-1h2.998C8.552 20 9 19.552 9 18.998l4-13.996C13 4.448 12.552 4 11.998 4H9z" fillRule="evenodd"></path>
                            </svg>
                        </i>
                    </div>
                    <div className={ this.getItemsCls('textDecoration', 'underline') } onClick={ this.handleChange('textDecoration', 'underline') }>
                        <i className={ `${ sprefix }-fontStyle-icon` }>
                            <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                <path d="M6 21.001V19h12v2.001H6zM6 3h2v8.504A3.496 3.496 0 0 0 11.496 15h1A3.503 3.503 0 0 0 16 11.496V3h2v8.998A5.001 5.001 0 0 1 12.998 17h-2.003A4.995 4.995 0 0 1 6 12.005V3z" fillRule="evenodd"></path>
                            </svg>
                        </i>
                    </div>
                    <div className={ this.getItemsCls('textDecoration', 'line-through') } onClick={ this.handleChange('textDecoration', 'line-through') }>
                        <i className={ `${ sprefix }-fontStyle-icon` }>
                            <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                <path d="M6.958 10A4.59 4.59 0 0 1 6.5 8c0-2.762 2.463-5 5.5-5 2.088 0 3.905 1.058 4.836 2.616l-1.702 1.049C14.56 5.679 13.37 5 12 5c-1.93 0-3.5 1.346-3.5 3 0 .768.338 1.469.894 2H6.958zM21 12v1h-4.212c.756.837 1.212 1.872 1.212 3 0 2.762-2.687 5-6 5-2.913 0-5.341-1.73-5.886-4.023l1.907-.674C8.231 17.793 9.968 19 12 19c2.168 0 4-1.374 4-3s-1.832-3-4-3H3v-1h18z" fillRule="evenodd"></path>
                            </svg>
                        </i>
                    </div>
                </div>
            </div>
        );
    }
}


