//       const user = {
//         id: 0,
//         userName: param.userName
//       }
//       localStorage.setItem('user', JSON.stringify(user));
//       dispatch({
//         type: "login",
//         user
//       });
//       history.push('/');
//     }
//   }
// }

@connect(
    state => state.login,
    dispatch => bindActionCreators({
        login: param => {
            return async dispatch => {
                const rs = await Ajax(`platform/login`, {
                    method: 'POST',
                    body: JSON.stringify(param)
                });
                if(rs.code === 0) {
                    localStorage.setItem('user', JSON.stringify(rs.data));
                    localStorage.setItem('token', rs.token);
                    dispatch({
                        type: "login",
                        user: rs.data
                    });
                    history.push('/');
                } else {
                    Message.warning({
                        msg: <div></div>
                    });
                }
                // Ajax(`platform/login`, {
                //     method: 'POST',
                //     body: JSON.stringify(param)
                // }).then(rs => {
                //     if(rs.code === 0) {
                //         localStorage.setItem('user', JSON.stringify(rs.data));
                //         localStorage.setItem('token', rs.token);
                //         dispatch({
                //             type: "login",
                //             user: rs.data
                //         });
                //         history.push('/');
                //     } else {
                //         Message.warning({
                //             msg: <div></div>
                //         });
                //     }
                // });
            }
        }
    }, dispatch)
)
export default class LoginPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: 'admin',
            password: 'admin'
        };
    }
    componentDidMount() {
        this.submit();
    }
    handleChange = (e) => {
        e.preventDefault();
        this.setState({ [e.target.name]: e.target.value });
    };
    submit = (e) => {
        // e.preventDefault();
        if (this.state.userName == '' || this.state.password == '') {
            Message.warning({
                msg: <div></div>
            });
            return;
        }
        this.props.login(this.state);
    };
    render() {
        return (
            <div className="login" onSubmit={this.submit}>
                <Form onSubmit={this.handleSubmit} className="login-form">
                    <div style={{ padding: '10px' }}>
                        <Input type="text" name="userName" value={this.state.userName} onChange={this.handleChange} placeholder="admin" />
                    </div>
                    <div style={{ padding: '10px' }}>
                        <Input type="password" name="password" value={this.state.password} onChange={this.handleChange} placeholder="admin" />
                    </div>
                    <div style={{ padding: '10px' }}>
                        <Button type="submit">Login In</Button>
                    </div>
                </Form>
            </div>
        );
    }
};



