import React, { Component } from 'react';
import PropTypes from 'prop-types';
class Ruler extends Component {
    static propTypes = {
        //onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui',
        offsetX: 0,
        offsetY: 0,
        fontColor: '#999',
        //onChange: () => { }
    };
    constructor(props) {
        super(props);
        let size = props.size || props.defaultSize;
        size = size || {
            width: 100,
            height: 100
        };
        let zoom = props.zoom || props.defaultZoom;
        zoom = zoom || 1;
        this.intervals = [];
        for(let i = .1; i < 1E5; i *= 10) {
            this.intervals.push(1 * i);
            this.intervals.push(2 * i);
            this.intervals.push(5 * i);
        }
        this.state = {
            size,
            zoom
        }
    }
    componentDidMount() {
        const { offsetX, offsetY } = this.props;
        this.refresh(this.state.zoom, {
            width: this.el.offsetWidth - offsetX,
            height: this.el.offsetHeight - offsetY - 4
        });
    }
    componentWillReceiveProps(nextProps) {
        if ('size' in nextProps) {
            if (nextProps.size !== this.state.size) {
                this.setState({
                    size: nextProps.size
                });
            }
        }
        if ('zoom' in nextProps) {
            if (nextProps.zoom !== this.state.zoom) {
                this.setState({
                    zoom: nextProps.zoom
                });
            }
        }
    }
    componentDidUpdate() {
        const { offsetX, offsetY } = this.props;
        this.refresh(this.state.zoom, {
            width: this.el.offsetWidth - offsetX,
            height: this.el.offsetHeight - offsetY - 4
        });
        //this.refresh(this.state.zoom, this.state.size);
    }
    componentWillUnmount() {

    }
    refresh(zoom, size) {
        this.updateRule(zoom, 'x', size);
        this.updateRule(zoom, 'y', size);
    }
    updateRule(zoom, type, rect) {
        const { fontColor, offsetX, offsetY } = this.props;
        let size = {
            width: this.el.offsetWidth - offsetX,
            height: this.el.offsetHeight - offsetY - 4,
            ...rect
        };
        let left = 0;
            //offset_x = 0,
            //offset_y = 0,
        let unit = 1;
        let lentype = type === 'x' ? 'width' : 'height';
        let content_d = left - 0;
        let limit = 30000;
        let ruler_len = size[lentype];
        let total_len = ruler_len;
            //canv_count = 1;
        let ctx_num = 0;
        let ctx_arr;
        let canvas = type === 'x' ? this.canvasX : this.canvasY;
        let ctx = canvas.getContext("2d");
        canvas.style[lentype] = total_len + 'px';
        ctx.fillStyle = "rgb(200,0,0)";
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        if (ruler_len >= limit) {
            let num = parseInt(ruler_len / limit, 10) + 1;
            ctx_arr = Array(num);
            ctx_arr[0] = ctx;
            for (let i = 1; i < num; i++) {
                canvas[lentype] = limit;
                ctx_arr[i] = ctx;
            }
            ctx[lentype] = ruler_len % limit;
            ruler_len = limit;
        }
        canvas[lentype] = ruler_len;

        let u_multi = unit * zoom,
            raw_m = 50 / u_multi,
            multi = 1;
        for (let i = 0; i < this.intervals.length; i++) {
            let num = this.intervals[i];
            multi = num;
            if (raw_m <= num) {
                break;
            }
        }

        let big_int = multi * u_multi;
        ctx.font = "normal 9px 'Lucida Grande', sans-serif";
        //ctx.font = "normal 9px";
        ctx.fillStyle = fontColor;

        let ruler_d = ((content_d / u_multi) % multi) * u_multi, label_pos = ruler_d - big_int;

        for (; ruler_d < total_len; ruler_d += big_int) {
            label_pos += big_int;
            //let real_d = ruler_d - content_d;
            let cur_d = Math.round(ruler_d) + .5;
            if (type === 'x') {
                ctx.moveTo(cur_d, 15);
                ctx.lineTo(cur_d, 0);
            } else {
                ctx.moveTo(15, cur_d);
                ctx.lineTo(0, cur_d);
            }
            let num = (label_pos - content_d) / u_multi, label;
            if (multi >= 1) {
                label = Math.round(num);
            } else {
                let decs = (multi + '').split('.')[1].length;
                label = num.toFixed(decs) - 0;
            }

            if (label !== 0 && label !== 1000 && label % 1000 === 0) {
                label = (label / 1000) + 'K';
            }
            //if(label >= 0) {
                if (type === 'x') {
                    ctx.fillText(label, ruler_d + 2, 8);
                    ctx.fillStyle = fontColor;
                } else {
                    let str = (label + '').split('');
                    for (let i = 0; i < str.length; i++) {
                        ctx.fillText(str[i], 1, (ruler_d + 9) + i * 9);
                        ctx.fillStyle = fontColor;
                    }
                }
            //}
            

            let part = big_int / 10;
            for (let i = 1; i < 10; i++) {
                let sub_d = Math.round(ruler_d + part * i) + .5;
                if (ctx_arr && sub_d > ruler_len) {
                    ctx_num++;
                    ctx.stroke();
                    if (ctx_num >= ctx_arr.length) {
                        i = 10;
                        ruler_d = total_len;
                        continue;
                    }
                    ctx = ctx_arr[ctx_num];
                    ruler_d -= limit;

                    sub_d = Math.round(ruler_d + part * i) + .5;
                }

                let line_num = (i % 2) ? 12 : 10;                
                if (type === 'x') {
                    ctx.moveTo(sub_d, 15);
                    ctx.lineTo(sub_d, line_num);
                } else {
                    ctx.moveTo(15, sub_d);
                    ctx.lineTo(line_num, sub_d);
                }
            }
        }
        ctx.strokeStyle = fontColor;
        ctx.stroke();
    }
    render() {
        const { props, state } = this;
        const { sprefix } = props;
        return (
            <div className={`${ sprefix }-ruler`} ref={
                ref => {
                    this.el = ref;
                }
            }>
                <div className={`${ sprefix }-ruler-x`} style={{paddingLeft: props.offsetX + 'px'}}>
                    <canvas ref={
                        ref => {
                            this.canvasX = ref;
                        }
                    }></canvas>
                </div>
                <div className={`${ sprefix }-ruler-y`} style={{paddingTop: props.offsetY + 'px'}}>
                    <canvas ref={
                        ref => {
                            this.canvasY = ref;
                        }
                    }></canvas>
                </div>
            </div>
        );
    }
}
export default Ruler;



