import React, { Component } from 'react';
import PropTypes from 'prop-types';
class Selection extends Component {
    static propTypes = {
        //onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui',
        // attr: {
        //     fill: 'none',
        //     stroke: '#666',
        //     //strokeDasharray: '3,2',
        //     display: 'inline',
        //     x: 0,
        //     y: 0,
        //     width: 0,
        //     height: 0
        // }
        //onChange: () => { }
    };
    constructor(props) {
        super(props);

        let attr = props.attr || props.defaultAttr;
        attr = {
            fill: 'none',
            stroke: '#666',
            //strokeDasharray: '3,2',
            display: 'inline',
            x: 0,
            y: 0,
            width: 0,
            height: 0,
            ...attr
        };
        //let zoom = props.zoom || props.defaultZoom;
        //zoom = zoom || 1;
        this.dx = 0;
        this.dy = 0;
        this.startX = 0;
        this.startY = 0;
        this.endX = 0;
        this.endY = 0;
        this.state = {
            attr
        }
    }
    componentDidMount() {
        this.contain = this.svg.parentNode;
        this.contain.addEventListener('mousedown', this.handleMouseDown, false);
    }
    componentWillReceiveProps(nextProps) {
        if ('attr' in nextProps) {
            if (nextProps.attr !== this.state.attr) {
                this.setState({
                    attr: nextProps.attr
                });
            }
        }
    }
    componentDidUpdate() {
        
    }
    componentWillUnmount() {
        this.contain.removeEventListener('mousedown', this.handleMouseDown);
    }
    handleMouseMove = (e) => {
        this.endX = e.pageX;
        this.endY = e.pageY;
        let x = this.startX - this.dx + this.contain.scrollLeft, 
            y = this.startY - this.dy + this.contain.scrollTop, 
            width = Math.abs(this.endX - this.startX),
            height = Math.abs(this.endY - this.startY);
        if(this.endX - this.startX < 0) {
            x = x - width;
        }
        if(this.endY - this.startY < 0) {
            y = y - height;
        }
        this.currentRect = {
            x,
            y,
            width,
            height
        }
        this.setState({
            attr: {
                ...this.state.attr,
                ...this.currentRect
            }
        });
        this.onMouseMove(e);
    }
    handleMouseUp = (e) => {
        this.setState({
            attr: {
                ...this.state.attr,
                width: 0,
                height: 0
            }
        });
        window.document.removeEventListener('mousemove', this.handleMouseMove);
        window.document.removeEventListener('mouseup', this.handleMouseUp);
        this.onMouseUp(e);
    }
    handleMouseDown = (e) => {
        e.preventDefault();
        if(e.button === 2) {
            return;
        }
        this.current = e.target;
        if(!this.validate()) {
            return;
        }
        let containRect = this.contain.getBoundingClientRect();
        this.dx = containRect.x;
        this.dy = containRect.y;
        this.svg.style.width = (containRect.width + this.contain.scrollLeft) + 'px';
        this.svg.style.height = (containRect.height + this.contain.scrollTop) + 'px';
        this.startX = e.pageX;
        this.startY = e.pageY;
        window.document.addEventListener('mousemove', this.handleMouseMove, false);
        window.document.addEventListener('mouseup', this.handleMouseUp, false);
        this.onMouseDown(e);
    }
    onMouseDown(e) {
        if (this.props.onMouseDown) {
            this.props.onMouseDown(e, this);
        }
    }
    onMouseMove(e) {
        if (this.props.onMouseMove) {
            this.props.onMouseMove(e, this);
        }
    }
    onMouseUp(e) {
        if (this.props.onMouseUp) {
            this.props.onMouseUp(e, this);
        }
    }
    static rectsIntersect(r1, r2, zoom) {
        if(!r1 || !r2) {
            return false;
        }
        let scale = zoom || 1;
        return r2.x < (r1.x / scale + r1.width / scale) 
            && (r2.x + r2.width) > r1.x / scale 
            && r2.y < (r1.y / scale + r1.height / scale) 
            && (r2.y + r2.height) > r1.y / scale;
    }
    validate(e) {
        return true;
    }
    render() {
        const { props, state } = this;
        const { sprefix } = props;
        const { attr } = state;
        return (
            <svg className={`${sprefix}-selection`} ref={ ref => { this.svg = ref; } }>
                <rect strokeDasharray="3,2" { ...attr } style={{ pointerEvents: "none" }} ref={ ref => { this.rect = ref; } }></rect>
            </svg>
        );
    }
}
export default Selection;



