const DaoUtil = {
    getList: async function(tableName, option = {}) {
        let p = window.IndexedDB.get(tableName, option).then((json) => {
            let data = json;
            let code = data ? 0 : -1;
            let msg = data ? 'success' : 'List';
            return {
                code,
                msg,
                data: data.reverse()
            };
        }).catch(error => {
            return {
                code: -1,
                msg: error
            };
        });
        return p;
    },
    get: async function(tableName, option = {}) {
        let p = window.IndexedDB.get(tableName, option).then((json) => {
            let data = json[0];
            let code = data ? 0 : -1;
            let msg = data ? 'success' : '';
            return {
                code,
                msg,
                data
            };
        }).catch(error => {
            return {
                code: -1,
                msg: error
            };
        });
        return p;
    },
    save: async function(tableName, option = {}) {
        let p = window.IndexedDB.save(tableName, option).then((json) => {
            let data = json;
            let code = data ? 0 : -1;
            let msg = data ? 'success' : '';
            return {
                code,
                msg,
                data
            };
        }).catch(error => {
            return {
                code: -1,
                msg: error
            };
        });
        return p;
    },
    update: async function(tableName, option = {}) {
        let p = window.IndexedDB.update(tableName, option).then((json) => {
            let data = json;
            let code = data ? 0 : -1;
            let msg = data ? 'success' : '';
            return {
                code,
                msg,
                data
            };
        }).catch(error => {
            return {
                code: -1,
                msg: error
            };
        });
        return p;
    },
    delete: async function(tableName, id) {
        let p = window.IndexedDB.delete(tableName, id).then((json) => {
            let data = json;
            let code = data ? 0 : -1;
            let msg = data ? 'success' : '';
            return {
                code,
                msg,
                data
            };
        }).catch(error => {
            return {
                code: -1,
                msg: error
            };
        });
        return p;
    }
};

export default DaoUtil;


