import React, { Component } from 'react';
import PropTypes from 'prop-types';

class ColorPanel extends Component {
    static propTypes = {
        defaultValue: PropTypes.string,
        type: PropTypes.string,
        colors: PropTypes.array
    };
    static defaultProps = {
        sprefix: 'dwrui',
        type: 'gradient',
        colors: [
            "#ffffff", "#fafafa", "#f5f5f5", "#e8e8e8", "#d9d9d9", "#bfbfbf", "#8c8c8c", "#595959", "#262626", "#000000",
            "#e5f7ff", "#b8e7ff", "#8ed4ff", "#65bfff", "#39a7ff", "#008dff", "#006adc", "#004db6", "#00378e", "#002568",
            "#e8f5e9", "#c8e6c9", "#a5d6a7", "#81c783", "#66bb6a", "#4caf50", "#42a047", "#388e3c", "#2f7d32", "#1b5e20",
            "#fffbe5", "#fff2b5", "#ffe689", "#ffd75c", "#ffc627", "#fcae00", "#d68900", "#ae6900", "#884d00", "#623400",
            "#fff1f0", "#ffccc6", "#ffa39c", "#ff7772", "#ff4b49", "#f81d22", "#d10d18", "#aa0212", "#84000f", "#5d000f",
            "#f3e5f5", "#e1bee7", "#ce93d8", "#ba68c8", "#ab47bc", "#9c26b0", "#8e24aa", "#7b1fa2", "#6a1b9a", "#4a148c"
        ]
    };
    constructor(props) {
        super(props);
        this.rWidth = 224;
        this.rHeight = 168;
        this.gradientSliderBarWidth = 224;
        this.angleType = {
            'to top': 0,
            'to top right': 45,
            'to right': 90,
            'to bottom right': 135,
            'to bottom': 180,
            'to bottom left': 225,
            'to left': 270,
            'to top left': 315,
            'radial': 180
            //'radial': 'at center center'
        };
        this.cacheValues = {};
        let value = props.value || props.defaultValue || '#FFFFFF';
        this.state = this.getStateFormValue(value);
        this.cacheValues = this.state.values;
    }
    componentDidMount() {
        this.updateAlphaBr();
    }
    componentWillReceiveProps(nextProps) {
        if('value' in nextProps) {
            //if(nextProps.value !== this.state.value) {
                const {
                    colorType,
                    values,
                    angle
                } = this.getStateFormValue(nextProps.value || '#FFFFFF');
                this.cacheValues[colorType] = values[colorType];
                this.setState({
                    colorType,
                    values: this.cacheValues,
                    angle
                });
            //}
        }
    }
    componentDidUpdate() {
        this.updateAlphaBr();
    }
    updateAlphaBr() {
        const { r, g, b, a } = this.state.color;
        this.alphaInput.style.setProperty("--start-value", `rgba(${ r },${ g },${ b },0)`);
        this.alphaInput.style.setProperty("--end-value", `rgba(${ r },${ g },${ b })`);
    }
    getStateFormValue(value) {
        const {
            angle,
            colorType,
            values,
            hex,
            hue,
            rgba,
            pos
        } = this.getParamFormValue(value);
        return {
            colorType,
            angle,
            values,
            drag: {
                'linear-gradient': 0,
                'radial-gradient': 0
            },
            color: {
                hex,
                hue,
                rgba,
                ...rgba,
                pos
            }
        };
    }
    getParamFormValue(value) {
        // if(value === undefined || value === null) {
        //     value = '#FFFFFF';
        // }
        let { values, color, gradient, colorType, angle } = this.getColor(value);
        if(!gradient || !gradient.length) {
            gradient = [{
                pos: {
                    x: 0,
                    y: 0
                },
                value: '#FFFFFF'
            }, {
                pos: {
                    x: 100,
                    y: 0
                },
                value: '#000000'                    
            }];
        }
        let rgba = this.getRgba(color);
        let hex = this.rgbaToHex(rgba);
        let hsv = this.rgbToHsv(rgba);
        let hue = hsv.h;
        let left = hsv.s * this.rWidth / 100;
        let top = (100 - hsv.v) * this.rHeight / 100;
        return {
            angle,
            colorType,
            color,
            values,
            hex,
            hue,
            rgba,
            ...rgba,
            gradient,
            pos: [left, top]
        }
    }
    onChange = (param) => {
        if(this.props) {
            this.props.onChange(param.values[param.colorType]);
        }
    }
    getColor(value) {
        let color = '#FFFFFF', angle, item = this.getParsedGradient(value), gradient = [], colorType, values = {
            'normal': '#FFFFFF',
            'linear-gradient': 'linear-gradient(rgb(255, 255, 255) 0%, rgb(0, 0, 0) 100%)',
            'radial-gradient': 'radial-gradient(at center center, rgb(255, 255, 255) 0%, rgb(0, 0, 0) 100%)'
        };
        if (item && item.length) {
            item = item[0];
            let colors = item.colors;
            angle = item.direction;
            colors.forEach(item => {
                gradient.push({
                    pos: {
                        x: parseFloat(item.pos, 10),
                        y: 0
                    },
                    value: item.color
                });
            });
            colorType = item.type;
            color = colors[0].color;
        } else {
            colorType = 'normal';
            color = value;
        }
        if(angle === undefined) {
            angle = 180;
        }
        values[colorType] = value;
        return {
            color,
            angle,
            values,
            gradient,
            colorType
        };
    }
    getParsedGradient(k) {
        var a = function (l, j) {
            var k, m = "";
            for (k = 0; k < l.length; k++) {
                if (typeof l[k] === "string") {
                    m += l[k];
                } else {
                    m += l[k].source;
                }
            }
            return new RegExp(m, j);
        };
        var c = function () {
            var s = "gi"
                , j = /(?:[+-]?\d*\.?\d+)(?:deg|grad|rad|turn)/
                ,
                l = /(?:to\s+)?((?:(?:left|right|top|bottom|ellipse at center|center, ellipse cover|)(?:\s+(?:top|bottom))?))/
                , x = /\s*,\s*/
                , t = /\#(?:[A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})/
                , r = /\(\s*(?:[0-9]{1,3}\s*,\s*){1}\s*(?:[0-9]{1,3}(?:%)?\s*,\s*){1}[0-9]{1,3}(?:%)?\s*\)/
                ,
                q = /\(\s*(?:[0-9]{1,3}\s*,\s*){1}\s*(?:[0-9]{1,3}(?:%)?\s*,\s*){2}\s*(?:[0-9]{1,3}(?:%)?(?:[.][0-9]{1,3})?\s*)\)/
                , u = /(?:[+-]?\d*\.?\d+)(?:%|[a-z]+)?/
                , v = /[_A-Za-z-][_A-Za-z0-9-]*/
                , k = a(["(?:", t, "|", "(?:rgb|hsl)", r, "|", "(?:rgba|hsla)", q, "|", v, ")"], "")
                , w = a([k, "(?:\\s+", u, ")?"], "")
                , o = a(["(?:", w, x, ")*", w], "")
                , n = a(["(?:(", j, ")|", l, ")"], "")
                , m = a(["(", n, ")", x, "(", o, ")"], s)
                , p = a(["\\s*(", k, ")", "(?:\\s+", "(", u, "))?", "(?:", x, "\\s*)?"], s);
            return {
                gradientSearch: m,
                colorsSearch: p
            }
        };
        var i = function (k, m) {
            var j, n, o, l;
            n = k.gradientSearch.exec(m);
            if (n !== null) {
                j = {
                    //string: n[0],
                    colors: []
                };
                if (!!n[1]) {
                    j.direction = n[1];
                    if (j.direction.indexOf("ellipse") !== -1 || j.direction.indexOf("circle") !== -1) {
                        j.direction = "radial";
                        j.type = 'radial-gradient';
                        //j.radialDirection = n[1]
                    }
                }
                // if (!!n[2]) {
                //     j.angle = n[2];
                // }
                // if (!!n[3]) {
                //     j.sideCorner = n[3]
                // }
                o = k.colorsSearch.exec(n[4]);
                while (o !== null) {
                    l = {
                        color: o[1]
                    };
                    if (!!o[2]) {
                        l.pos = o[2]
                    }
                    j.colors.push(l);
                    o = k.colorsSearch.exec(n[4])
                }
            }
            return j
        };
        var u;
        var n = [];
        var r = [];
        if (!k || k.length == 0 || k.indexOf("gradient(") == -1) {
            return [];
        }
        var v = /(?:\s*)(?:linear|radial)-gradient\s*\(((?:\([^\)]*\)|[^\)\(])*)\)/g;
        var q;
        while (q = v.exec(k)) {
            n.push(q[0]);
        }
        if (n.length == 0) {
            return [];
        }
        for (let o = 0; o < n.length; o++) {
            u = n[o];
            if (u.length == 0) {
                break;
            }
            if (u.indexOf("to ") == -1) {
                u = u.replace("linear-gradient(", "linear-gradient(to bottom, ");
            }
            var l = c();
            var s = /.*gradient\s*\(((?:\([^\)]*\)|[^\)\(]*)*)\)/;
            var q = s.exec(u);
            if (q !== null) {
                var t = i(l, q[1]);
                if (t) {
                    if (!t.direction && u.indexOf("radial") !== -1) {
                        t.type = 'radial-gradient';
                        t.direction = "radial";
                    } else {
                        t.type = 'linear-gradient';
                    }
                    for (let p = 0; p < t.colors.length; p++) {
                        if (typeof t.colors[p].pos !== "undefined") {
                            t.colors[p].pos = t.colors[p].pos.replace("%", "");
                        } else {
                            if (t.colors.length == 1) {
                                t.colors[p].pos = "100";
                            } else {
                                t.colors[p].pos = "" + ((p / (t.colors.length - 1)) * 100);
                            }
                        }
                        if (typeof t.direction !== "undefined") {
                            t.direction = t.direction.replace("deg", "");
                        }
                    }
                    r.push(t);
                }
            }
        }
        return r;
    }
    getHex(v) {
        let hex;
        if (v.startsWith('rgb') || v.startsWith('RGB')) {
            hex = this.rgbaToHex(v);
        } else if (v.startsWith('#')) {
            hex = v;
        } else if (this.validHex(v)) {
            hex = '#' + hex;
        }
        return hex;
    }
    validHex(hex) {
        return /^[0-9a-fA-F]{3,6}$/.test(hex);
    }
    validRgb(v) {
        if(isNaN(v)) {
            return false;
        }
        let value = parseInt(v, 10);
        if (value >= 0 && value <= 255) {
            return value;
        }
        return false;
    }
    validApacity(v) {
        let value = Number(v);
        if (value >= 0 && value <= 1) {
            return value;
        }
        return false;
    }
    getRgba(v) {
        let rgba;
        if (v.startsWith('rgb') || v.startsWith('RGB')) {
            rgba = this.parseRgba(v);
        } else if (v.startsWith('#')) {
            let value = v.slice(1);
            if (this.validHex(value)) {
                rgba = this.hexToRgba(value);
            }
        } else if (this.validHex(v)) {
            rgba = this.hexToRgba(v);
        }
        return rgba;
    }
    rgbaToString(rgba) {
        return (rgba.a == null || rgba.a == 1) ? `rgb(${rgba.r},${rgba.g},${rgba.b})` : `rgba(${rgba.r},${rgba.g},${rgba.b},${rgba.a})`;
    }
    parseRgba(value) {
        let rgba;
        if (value.startsWith('rgb') || value.startsWith('RGB')) {
            let cs = value.split('(')[1].split(')')[0].split(',');
            rgba = {
                r: parseInt(cs[0], 10),
                g: parseInt(cs[1], 10),
                b: parseInt(cs[2], 10),
                a: cs.length > 3 ? parseFloat(cs[3]) : 1
            };
            return rgba;
        }
        //rgba = this.hexToRgba(value);
        return rgba;
    }
    hsvToRgb(ah, as = 1, av = 1) {
        let h = ah / 60 % 6, s = as, v = av;
        if (as > 1) {
            s = as / 100;
        }
        if (av > 1) {
            v = av / 100;
        }
        let i = Math.floor(h),
            f = h - i,
            p = v * (1 - s),
            q = v * (1 - f * s),
            t = v * (1 - (1 - f) * s),
            mod = i % 6,
            r = [v, q, p, p, t, v][mod],
            g = [t, v, v, q, p, p][mod],
            b = [p, p, t, v, v, q][mod];
        return {
            r: Math.floor(r * 255),
            g: Math.floor(g * 255),
            b: Math.floor(b * 255)
        };
    }
    rgbToHsv(rgb) {
        let r = rgb.r / 255;
        let g = rgb.g / 255;
        let b = rgb.b / 255;
        let max = Math.max(r, g, b), min = Math.min(r, g, b);
        let h, s, v = max;
        let d = max - min;
        s = max === 0 ? 0 : d / max;
        if (max == min) {
            h = 0;
        } else {
            switch (max) {
                case r:
                    h = (g - b) / d + (g < b ? 6 : 0);
                    break;
                case g:
                    h = (b - r) / d + 2;
                    break;
                case b:
                    h = (r - g) / d + 4;
                    break;
            }
            h /= 6;
        }
        return {
            h: Math.floor(h * 360),
            s: Math.floor(s * 100),
            v: Math.floor(v * 100)
        };
    }
    hexToRgba(HEX) {
        let hex = HEX.indexOf("#") > -1 ? HEX.slice(1) : HEX;
        if (hex.length === 3) {
            hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
        }
        return {
            r: parseInt(hex.slice(0, 2), 16) || 0,
            g: parseInt(hex.slice(2, 4), 16) || 0,
            b: parseInt(hex.slice(4, 6), 16) || 0,
            a: (parseInt(hex.slice(6, 8), 16) || 255) / 255
        };
    }
    rgbaToHex(rgba) {
        return (
            (rgba.r < 16 ? '0' : '') + rgba.r.toString(16) +
            (rgba.g < 16 ? '0' : '') + rgba.g.toString(16) +
            (rgba.b < 16 ? '0' : '') + rgba.b.toString(16)
        ).toUpperCase();
    }
    getGradientRange(startColor, endColor, color) {
        let sRgba = this.getRgba(startColor);
        let eRgba = this.getRgba(endColor);
        let rgba = this.getRgba(color);
        let dr = rgba.r - sRgba.r;
        let dg = rgba.g - sRgba.g;
        let db = rgba.b - sRgba.b;
        let max = Math.max(dr, dg, db);
        let range;
        if(max === dg) {
            range = (eRgba.g - sRgba.g) ? (dg / (eRgba.g - sRgba.g)) : 0;
        } else if(max === db) {
            range = (eRgba.b - sRgba.b) ? (db / (eRgba.b - sRgba.b)) : 0;
        } else {
            range = (eRgba.r - sRgba.r) ? (dr / (eRgba.r - sRgba.r)) : 0;
        }
        return range; 
    }
    getGradientArray(startColor, endColor, step) {
        const rs = [];
        if(step < 2) {
            rs.push(startColor);
        } else {
            let sRgba = this.getRgba(startColor);
            let eRgba = this.getRgba(endColor);
            let dr = (eRgba.r - sRgba.r) / (step - 1);
            let dg = (eRgba.g - sRgba.g) / (step - 1);
            let db = (eRgba.b - sRgba.b) / (step - 1);
            //let da = (eRgba.a - sRgba.a) / (step - 1);
            for(let i = 0; i < step; i++) {
                rs.push(this.rgbaToString({
                    r: parseInt(dr * i + sRgba.r),
                    g: parseInt(dg * i + sRgba.g),
                    b: parseInt(db * i + sRgba.b),
                    //a: parseInt(da * i + sRgba.a),
                }));
            }
        }
        return rs;
    }
    getGradientColor(startColor, endColor, range) {
        let sRgba = this.getRgba(startColor);
        let eRgba = this.getRgba(endColor);
        let dr = (eRgba.r - sRgba.r) * range;
        let dg = (eRgba.g - sRgba.g) * range;
        let db = (eRgba.b - sRgba.b) * range;
        //let da = (eRgba.a - sRgba.a) * range;
        let rgba = {
            r: parseInt(dr + sRgba.r, 10),
            g: parseInt(dg + sRgba.g, 10),
            b: parseInt(db + sRgba.b, 10)
            //a: parseInt(da + sRgba.a, 10),
        };
        return this.rgbaToString(rgba);
    }
    setPos(pos) {
        const { rgba, hue } = this.state.color;
        let rect = this.panel.getBoundingClientRect();
        let left = pos.x - rect.left - (window.pageXOffset || document.documentElement.scrollLeft || document.body.scrollLeft || 0);
        let top = pos.y - rect.top - (window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0);
        left = Math.max(0, left);
        left = Math.min(left, this.rWidth);
        top = Math.max(0, top);
        top = Math.min(top, this.rHeight);
        if(left !== this.state.color.pos[0] || top !== this.state.color.pos[1]) {
            let s = left * 100 / this.rWidth;
            let v = (this.rHeight - top) * 100 / this.rHeight;
            let newRgba = this.hsvToRgb(hue, s, v);
            newRgba.a = rgba.a;
            let hex = this.rgbaToHex(newRgba);
            this.doUpdate({
                color: {
                    ...this.state.color,
                    pos: [left, top],
                    rgba: newRgba,
                    //...newRgba,
                    hex
                }
            });
        }
    }
    handleMouseDown = e => {
        e.preventDefault();
        this.isMove = true;
        this.setPos({
            x: e.pageX || e.clientX,
            y: e.pageY || e.clientY
        });
        document.addEventListener('mousemove', this.handleMouseMove);
        document.addEventListener('mouseup', this.handleMouseUp);
    };
    handleMouseMove = e => {
        e.preventDefault();
        if (this.isMove) {
            this.setPos({
                x: e.pageX || e.clientX,
                y: e.pageY || e.clientY
            });
        }
    };
    handleMouseUp = e => {
        e.preventDefault();
        this.isMove = false;
        document.removeEventListener('mousemove', this.handleMouseMove);
        document.removeEventListener('mouseup', this.handleMouseUp);
    };
    handleArea = e => {
        e.preventDefault();
        const { color } = this.state;
        let hue = Number(e.target.value);
        let s = color.pos[0] * 100 / this.rWidth;
        let v = (this.rHeight - color.pos[1]) * 100 / this.rHeight;
        let newRgba = this.hsvToRgb(hue, s, v);
        let hex = this.rgbaToHex(newRgba);
        newRgba.a = color.rgba.a;
        this.doUpdate({
            color: {
                ...color,
                hue,
                rgba: newRgba,
                //...newRgba,
                hex
            }
        });
    };
    handleAlpha = e => {
        e.preventDefault();
        const { color } = this.state;
        let newRgba = {
            r: color.r,
            g: color.g,
            b: color.b,
            a: Number(e.target.value) / 100
        };
        this.doUpdate({
            color: {
                ...color,
                rgba: newRgba,
                //...newRgba
            }
        });
    };
    handleHex = e => {
        e.preventDefault();
        const { color } = this.state;
        let hex = e.target.value;
        this.setState({
            color: {
                ...color,
                hex
            }
        });
    };
    handleHexPress = e => {
        if(e.nativeEvent.which === 13) {
            e.target.blur();
        }
    };
    handleHexBlur = e => {
        e.preventDefault();
        const { color } = this.state;
        if(this.validHex(this.state.color.hex)) {
            let newRgba = this.hexToRgba(color.hex);
            newRgba.a = color.rgba.a;
            let hsv = this.rgbToHsv(newRgba);
            let hue = hsv.h;
            let left = hsv.s * this.rWidth / 100;
            let top = (100 - hsv.v) * this.rHeight / 100;
            this.doUpdate({
                color: {
                    ...color,
                    hex: this.rgbaToHex(newRgba),
                    rgba: newRgba,
                    //...newRgba,
                    hue,
                    pos: [left, top]
                }
            });
        } else {
            this.setState({
                color: {
                    ...color,
                    hex: this.rgbaToHex(color.rgba)
                }
            });
        }
    };
    handleRgba = v => {
        return e => {
            e.preventDefault();
            const { color } = this.state;
            this.setState({
                color: {
                    ...color,
                    [v]: e.target.value
                }
            });
        };
    };
    handleRgbaPress = e => {
        if(e.nativeEvent.which === 13) {
            e.target.blur();
        }
    };
    handleRgbaBlur = v => {
        return e => {
            e.preventDefault();
            const { color } = this.state;
            let value = v === 'a' ? this.validApacity(e.target.value) : this.validRgb(e.target.value);
            let newRgba = {
                r: color.rgba.r,
                g: color.rgba.g,
                b: color.rgba.b,
                a: color.rgba.a
            };
            if(value !== false) {
                newRgba[v] = value;
                let hsv = this.rgbToHsv(newRgba);
                let hue = hsv.h;
                let left = hsv.s * this.rWidth / 100;
                let top = (100 - hsv.v) * this.rHeight / 100;
                this.doUpdate({
                    color: {
                        ...color,
                        hex: this.rgbaToHex(newRgba),
                        rgba: newRgba,
                        //...newRgba,
                        hue,
                        pos: [left, top]
                    }
                });
            } else {
                newRgba[v] = color.rgba[v];
                this.setState({
                    color: {
                        ...color,
                        rgba: newRgba,
                        ...newRgba
                    }
                });
            }
        };
    };
    handleColorType = colorType => {
        return e => {
            e.preventDefault();
            if(colorType !== this.state.colorType) {
                let newState;
                if(colorType === 'normal') {
                    let { hex, hue, rgba, pos } = this.getParamFormValue(this.state.values[colorType]);
                    newState = {
                        ...this.state,
                        colorType,
                        color: {
                            hex,
                            hue,
                            rgba,
                            ...rgba,
                            pos
                        }
                    }
                } else if(colorType === 'linear-gradient' || colorType === 'radial-gradient') {
                    let { gradient } = this.getParamFormValue(this.state.values[colorType]);
                    let { hex, hue, rgba, pos } = this.getParamFormValue(gradient[this.state.drag[colorType]].value);
                    newState = {
                        ...this.state,
                        colorType,
                        color: {
                            hex,
                            hue,
                            rgba,
                            ...rgba,
                            pos
                        }
                    }
                }
                //console.log(newState);
                this.doUpdate(newState);
            }
        };
    };
    handleClick = color => {
        return e => {
            e.preventDefault();
            const { hex, hue, rgba, pos, colorType } = this.getParamFormValue(color);
            const newState = {
                ...this.state,
                color: {
                    ...this.state.color,
                    hex,
                    hue,
                    rgba,
                    pos
                }
            };
            //newState.values[colorType] = this.toColor(newState);
            this.doUpdate(newState);
        };
    };
    handleAngle = e => {
        e.preventDefault();
        const angle = e.target.value;
        this.doUpdate({
            ...this.state,
            angle
        });
    };
    getAngle(angle) {
        return this.angleType[angle] !== undefined ? this.angleType[angle] : angle;
    }
    doUpdate(param) {
        param.color.r = param.color.rgba.r;
        param.color.g = param.color.rgba.g;
        param.color.b = param.color.rgba.b;
        param.color.a = param.color.rgba.a;
        let newState = {
            ...this.state,
            ...param
        };
        newState.values[newState.colorType] = this.toColor(newState);
        this.setState(newState);
        this.onChange(newState);
        this.cacheValues = newState.values;
    }
    toColor(newState, gradient) {
        if(newState.colorType === 'normal') {
            return this.rgbaToString(newState.color.rgba);
        } else if(newState.colorType === 'linear-gradient') {
            if(gradient === undefined) {
                gradient = this.getParamFormValue(newState.values[newState.colorType]).gradient;
            }
            if(gradient[newState.drag[newState.colorType]]) {
                gradient[newState.drag[newState.colorType]].value = this.rgbaToString(newState.color.rgba);
            }
            const gradientItems = [];
            // gradient.sort((a, b) => {
            //     return Number(a.pos.x) - Number(b.pos.x);
            // });
            gradient.forEach(item => {
                gradientItems.push(`${ item.value } ${ item.pos.x }%`);
            });
            if (newState.angle !== '' && !isNaN(newState.angle)) {
                gradientItems.unshift(this.getAngle(newState.angle) + 'deg');
            }
            return `linear-gradient(${ gradientItems.join(',') })`;
        } else if(newState.colorType === 'radial-gradient') {
            if(gradient === undefined) {
                gradient = this.getParamFormValue(newState.values[newState.colorType]).gradient;
            }
            if(gradient[newState.drag[newState.colorType]]) {
                gradient[newState.drag[newState.colorType]].value = this.rgbaToString(newState.color.rgba);
            }
            const gradientItems = [];
            // gradient.sort((a, b) => {
            //     return Number(a.pos.x) - Number(b.pos.x);
            // });
            gradient.forEach(item => {
                gradientItems.push(`${ item.value } ${ item.pos.x }%`);
            });
            return `radial-gradient(at center center,${ gradientItems.join(',') })`;
        }
    }
    handleMouseDownDrag = index => {
        return e => {
            e.preventDefault();
            this.dragStartX = e.pageX || e.clientX;
            //this.dragStartY = e.pageY || e.clientY;
            this.isDragMove = true;
            document.addEventListener('mousemove', this.handleMouseMoveDrag);
            document.addEventListener('mouseup', this.handleMouseUpDrag);
            if(index === this.state.drag[this.state.colorType]) {
                return false;
            }
            const { values, colorType } = this.state;
            const { gradient } = this.getParamFormValue(values[colorType]);
            const { hex, hue, rgba, pos } = this.getParamFormValue(gradient[index].value);
            const newState = {
                ...this.state,
                color: {
                    ...this.state.color,
                    hex,
                    hue,
                    rgba,
                    pos
                }
            };
            newState.drag[newState.colorType] = index;
            this.doUpdate(newState);
        };
    };
    handleMouseMoveDrag = e => {
        e.preventDefault();
        this.dragEndX = e.pageX || e.clientX;
        //this.dragEndY = e.pageY || e.clientY;
        if (this.isDragMove && (this.dragEndX - this.dragStartX !== 0)) {
            let x = e.pageX || e.clientX;
            //let y = e.pageY || e.clientY;
            let rect = this.gradientSliderBar.getBoundingClientRect();
            let left = x - rect.left - (window.pageXOffset || document.documentElement.scrollLeft || document.body.scrollLeft || 0);
            //let top = y - rect.top - (window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0);
            left = Math.max(0, left);
            left = Math.min(left, this.gradientSliderBarWidth);
            //top = Math.max(0, top);
            //top = Math.min(top, this.gradientSliderBarHeight);
            const { values, colorType, angle, drag } = this.state;
            const { gradient, rgba } = this.getParamFormValue(values[colorType]);
            gradient[drag[colorType]].pos.x = left * 100 / this.gradientSliderBarWidth;
            const newState = {
                ...this.state
            };
            newState.values[colorType] = this.toColor({
                drag,
                colorType,
                values,
                angle,
                color: {
                    rgba
                }
            }, gradient);
            this.doUpdate(newState);
        }
    };
    handleMouseUpDrag = e => {
        e.preventDefault();
        this.isDragMove = false;
        document.removeEventListener('mousemove', this.handleMouseMoveDrag);
        document.removeEventListener('mouseup', this.handleMouseUpDrag);
    };
    handleDoubleClickDrag = index => {
        return e => {
            e.preventDefault();
            const { values, colorType, angle, drag } = this.state;
            const { gradient, rgba } = this.getParamFormValue(values[colorType]);
            if(gradient.length <= 2) {
                return false;
            }
            gradient.splice(index, 1);
            const newState = {
                ...this.state
            };
            newState.values[colorType] = this.toColor({
                drag,
                colorType,
                values,
                angle,
                color: {
                    rgba
                }
            }, gradient);
            this.doUpdate(newState);
        };
    };
    handleAddDrag = e => {
        e.preventDefault();
        if (e.target === this.gradientSliderBar) {
            let x = e.pageX || e.clientX;
            //let y = e.pageY || e.clientY;
            let rect = this.gradientSliderBar.getBoundingClientRect();
            let left = x - rect.left - (window.pageXOffset || document.documentElement.scrollLeft || document.body.scrollLeft || 0);
            //let top = y - rect.top - (window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0);
            left = Math.max(0, left);
            left = Math.min(left, this.gradientSliderBarWidth);
            left = left * 100 / this.gradientSliderBarWidth;
            const { values, colorType, angle, drag } = this.state;
            const { gradient } = this.getParamFormValue(values[colorType]), defaultColor = '#FFFFFF';
            gradient.push({
                pos: {
                    x: left,
                    y: 0
                },
                value: defaultColor
            });
            gradient.sort((a, b) => {
                return Number(a.pos.x) - Number(b.pos.x);
            });
            let index;
            for(let i = 0; i < gradient.length; i++) {
                if(gradient[i].pos.x === left) {
                    index = i;
                    break;
                }
            }
            const { hex, hue, rgba, pos } = this.getParamFormValue(defaultColor);
            const newState = {
                ...this.state,
                color: {
                    ...this.state.color,
                    hex,
                    hue,
                    rgba,
                    pos
                }
            };
            newState.drag[newState.colorType] = index;
            newState.values[colorType] = this.toColor({
                drag,
                colorType,
                values,
                angle,
                color: {
                    rgba: newState.color.rgba
                }
            }, gradient);
            this.doUpdate(newState);
        }
    };
    renderHeader() {
        const { sprefix } = this.props, rs = [];
        ['normal', 'linear-gradient', 'radial-gradient'].forEach(item => {
            let cls = [`${ sprefix }-colorPicker-item`, `${ sprefix }-${ item }-btn`];
            if(item === this.state.colorType) {
                cls.push(`${ sprefix }-colorPicker-item-active`);
            }
            rs.push(
                <div key={ item } className={ cls.join(' ') } onClick={ this.handleColorType(item) }></div>
            );
        });
        return <div className={ `${ sprefix }-colorPicker-list` }>{ rs }</div>;
    }
    renderGradientBar(gradient) {
        const { sprefix } = this.props;
        const { drag, colorType } = this.state;
        const rs = [];
        gradient.forEach((item, index) => {
            let cls = [`${ sprefix }-colorPicker-gradient-slider-drag`];
            if(drag[colorType] === index) {
                cls.push(`${ sprefix }-colorPicker-gradient-slider-drag-selected`);
            }
            rs.push(
                <div
                    key={ index }
                    className={ cls.join(' ') }
                    style={{
                        left: item.pos.x * this.gradientSliderBarWidth / 100 + 'px',
                        top: item.pos.y + 'px'
                    }}
                    onMouseDown={ this.handleMouseDownDrag(index) }
                    onDoubleClick={ this.handleDoubleClickDrag(index) }
                >
                    <div className={ `${ sprefix }-colorPicker-gradient-slider-pointer` }></div>
                    <div className={ `${ sprefix }-colorPicker-gradient-slider-body` }>
                        <div
                            className={ `${ sprefix }-colorPicker-gradient-slider-body-inner` }
                            style={{
                                background: item.value
                            }}
                        ></div>
                    </div>
                </div>
            );
        });
        return rs;
    }
    renderGlobalColors() {
        const { sprefix, colors } = this.props;
        const rs = [];
        colors.forEach((item, index) => {
            rs.push(
                <div key={ index }
                    className={ `${ sprefix }-colorPicker-colors-item` }
                    onClick={ this.handleClick(item) }
                    style={{ backgroundColor: item }}
                ></div>
            );
        });
        return rs;
    }
    render() {
        const { sprefix, cls } = this.props;
        const { colorType, color, angle, values } = this.state;
        let hueColor = this.hsvToRgb(color.hue),
            prviewBg,
            gradientBarBg = '',
            gradientBar,
            angleDisplay;
        prviewBg = values[colorType];
        if(colorType === 'linear-gradient' || colorType === 'radial-gradient') {
            const { gradient } = this.getColor(values[colorType]), gradientItems = [];
            // gradient.sort((a, b) => {
            //     return Number(a.pos.x) - Number(b.pos.x);
            // });
            gradient.forEach(item => {
                gradientItems.push(`${ item.value } ${ item.pos.x }%`);
            });
            gradientBarBg = `linear-gradient(to right,${ gradientItems.join(',') })`;
            gradientBar = this.renderGradientBar(gradient);
        }
        return (
            <div className={ `${ sprefix }-colorPicker ${ sprefix }-${ this.props.type }-colorPicker${ cls !== undefined ? ' ' + cls : '' }` }>
                <div className={ `${ sprefix }-colorPicker-header` }>
                    { this.renderHeader() }
                </div>
                <div className={ `${ sprefix }-colorPicker-center` }>
                    <div className={ `${ sprefix }-colorPicker-gradient ${ sprefix }-${ colorType }-wrap` }>
                        <div className={ `${ sprefix }-colorPicker-gradient-bar-bg` }>
                            <div className={ `${ sprefix }-colorPicker-gradient-bar` } style={{ background: gradientBarBg }}></div>
                        </div>
                        <div className={ `${ sprefix }-colorPicker-gradient-slider-bar` }
                            ref={ (ref) => this.gradientSliderBar = ref }
                            onMouseDown={ this.handleAddDrag }
                        >
                            { gradientBar }
                        </div>
                        <div className={ `${ sprefix }-colorPicker-gradient-angle-wrap` }>
                            <input className={ `${ sprefix }-colorPicker-range ${ sprefix }-colorPicker-gradient-angle` }
                                type="range"
                                min="0"
                                max="360"
                                title={ angle }
                                value={ angle }
                                onChange={ this.handleAngle }
                                style={{ display: angleDisplay }}
                            />
                            <div className={ `${ sprefix }-colorPicker-gradient-angle-list` } style={{ display: 'none' }}>
                                <span className={ `${ sprefix }-colorPicker-gradient-angle-item` }>0</span>
                                <span className={ `${ sprefix }-colorPicker-gradient-angle-item` }>
                                    <input className={ `${ sprefix }-colorPicker-input ${ sprefix }-colorPicker-gradient-angle-input` } type="text"/>
                                </span>
                                <span className={ `${ sprefix }-colorPicker-gradient-angle-item` }>360</span>
                            </div>
                        </div>
                    </div>
                    <div className={ `${ sprefix }-colorPicker-center-inner` }>
                        <div></div>
                        <div className={ `${ sprefix }-colorPicker-panel` }  ref={ (ref) => this.panel = ref } onMouseDown={ this.handleMouseDown }>
                            <div className={ `${ sprefix }-colorPicker-hue` } style={{ backgroundColor: `rgb(${ hueColor.r },${ hueColor.g },${ hueColor.b })` }}>
                                <div className={ `${ sprefix }-colorPicker-saturation` }>
                                    <div className={ `${ sprefix }-colorPicker-brightness` }></div>
                                </div>
                            </div>
                            <div className={ `${ sprefix }-colorPicker-pointer` } style={{ left: color.pos[0] + 'px', top: color.pos[1] + 'px' }}></div>
                        </div>
                        <div className={ `${ sprefix }-colorPicker-fixed` }>
                            <div className={ `${ sprefix }-colorPicker-left` }>
                                <div className={ `${ sprefix }-colorPicker-area-slider-bg` }>
                                    <input className={ `${ sprefix }-colorPicker-range ${ sprefix }-colorPicker-area-slider` }
                                        type="range"
                                        min="0"
                                        max="360"
                                        value={ color.hue }
                                        onChange={ this.handleArea }
                                    />
                                </div>
                                <div className={ `${ sprefix }-colorPicker-alpha-slider-bg` }>
                                    <input className={ `${ sprefix }-colorPicker-range ${ sprefix }-colorPicker-alpha-slider` }
                                        type="range"
                                        min="0"
                                        max="100"
                                        value={ color.rgba.a * 100 }
                                        onChange={ this.handleAlpha }
                                        ref={ (ref) => this.alphaInput = ref }
                                    />
                                </div>
                            </div>
                            <div className={ `${ sprefix }-colorPicker-right` }>
                                <div className={ `${ sprefix }-colorPicker-preview-bg` }>
                                    <div className={ `${ sprefix }-colorPicker-preview` } style={{ background: prviewBg }}></div>
                                    {/* <div className={ `${ sprefix }-colorPicker-gradient-preview` } style={{ background: prviewBg }}></div> */}
                                </div>
                            </div>
                        </div>
                        <div style={{ overflow: 'hidden', marginTop: '5px' }}>
                            <div style={{ float: 'left', width: '30%', paddingLeft: '12px', position: 'relative', boxSizing: 'border-box' }}>
                                <span style={{ position: 'absolute', top: 0, left: 0, lineHeight: '26px' }}>#</span>
                                <input className={ `${ sprefix }-colorPicker-input ${ sprefix }-colorPicker-input-hex` }
                                    value={ color.hex }
                                    maxLength="6"
                                    type="text"
                                    onKeyPress={ this.handleHexPress }
                                    onBlur={ this.handleHexBlur }
                                    onChange={ this.handleHex }
                                    style={{ width: '100%', padding: '0 5px', outline: 'none', boxSizing: 'border-box' }}
                                />
                                <div className={ `${ sprefix }-colorPicker-label` } style={{ width: '100%', fontSize: '12px', textAlign: 'center' }}>HEX</div>
                            </div>
                            <div style={{ float: 'left', width: '70%', boxSizing: 'border-box' }}>
                                <div style={{ float: 'left', width: '25%', paddingLeft: '5px', boxSizing: 'border-box' }}>
                                    <input className={ `${ sprefix }-colorPicker-input ${ sprefix }-colorPicker-rgba-r` } 
                                        type="text"
                                        value={ color.r }
                                        maxLength="3"
                                        onKeyPress={ this.handleRgbaPress }
                                        onBlur={ this.handleRgbaBlur('r') }
                                        onChange={ this.handleRgba('r') }
                                        style={{ width: '100%', padding: '0 5px', outline: 'none', boxSizing: 'border-box' }}
                                    />
                                    <div className={ `${ sprefix }-colorPicker-label` } style={{ width: '100%', fontSize: '12px', textAlign: 'center' }}>R</div>
                                </div>
                                <div style={{ float: 'left', width: '25%', paddingLeft: '5px', boxSizing: 'border-box' }}>
                                    <input className={ `${ sprefix }-colorPicker-input ${ sprefix }-colorPicker-rgba-g` } 
                                        type="text"
                                        value={ color.g }
                                        maxLength="3"
                                        onKeyPress={ this.handleRgbaPress }
                                        onBlur={ this.handleRgbaBlur('g') }
                                        onChange={ this.handleRgba('g') }
                                        style={{ width: '100%', padding: '0 5px', outline: 'none', boxSizing: 'border-box' }}
                                    />
                                    <div className={ `${ sprefix }-colorPicker-label` } style={{ width: '100%', fontSize: '12px', textAlign: 'center' }}>G</div>
                                </div>
                                <div style={{ float: 'left', width: '25%', paddingLeft: '5px', boxSizing: 'border-box' }}>
                                    <input className={ `${ sprefix }-colorPicker-input ${ sprefix }-colorPicker-rgba-b` } 
                                        type="text" 
                                        value={ color.b }
                                        maxLength="3"
                                        onKeyPress={ this.handleRgbaPress }
                                        onBlur={ this.handleRgbaBlur('b') }
                                        onChange={ this.handleRgba('b') }
                                        style={{ width: '100%', padding: '0 5px', outline: 'none', boxSizing: 'border-box' }}
                                    />
                                    <div className={ `${ sprefix }-colorPicker-label` } style={{ width: '100%', fontSize: '12px', textAlign: 'center' }}>B</div>
                                </div>
                                <div style={{ float: 'left', width: '25%', paddingLeft: '5px', boxSizing: 'border-box' }}>
                                    <input className={ `${ sprefix }-colorPicker-input ${ sprefix }-colorPicker-rgba-a` } 
                                        type="text"
                                        value={ color.a }
                                        onKeyPress={ this.handleRgbaPress }
                                        onBlur={ this.handleRgbaBlur('a') }
                                        onChange={ this.handleRgba('a') }
                                        style={{ width: '100%', padding: '0 5px', outline: 'none', boxSizing: 'border-box' }}
                                    />
                                    <div className={ `${ sprefix }-colorPicker-label` } style={{ width: '100%', fontSize: '12px', textAlign: 'center' }}>A</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={ `${ sprefix }-colorPicker-footer` }>
                    <div className={ `${ sprefix }-colorPicker-global` }>
                        <input className={ `${ sprefix }-colorPicker-chk` } defaultChecked type="checkbox"/>
                        <div className={ `${ sprefix }-colorPicker-arrow-wrap` }>
                            <span className={ `${ sprefix }-colorPicker-arrow` }>
                                <svg viewBox="0 0 1024 1024" focusable="false" data-icon="caret-down" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                    <path d="M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z"></path>
                                </svg>
                            </span>
                            <span style={{ fontSize: '12px' }}>Global Colors</span>
                        </div>
                        <div className={ `${ sprefix }-colorPicker-list ${ sprefix }-colorPicker-colors` }>
                            { this.renderGlobalColors() }
                        </div>
                    </div>
                    <div className={ `${ sprefix }-colorPicker-recent` }>
                        <input className={ `${ sprefix }-colorPicker-chk` } type="checkbox"/>
                        <div className={ `${ sprefix }-colorPicker-arrow-wrap` }>
                            <span className={ `${ sprefix }-colorPicker-arrow` }>
                                <svg viewBox="0 0 1024 1024" focusable="false" data-icon="caret-down" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                    <path d="M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z"></path>
                                </svg>
                            </span>
                            <span style={{ fontSize: '12px' }}>Recent Colors</span>
                        </div>
                        <div className={ `${ sprefix }-colorPicker-list` }></div>
                    </div>
                </div>
                <input className={ `${ sprefix }-colorPicker-hidden` } type="hidden"/>
            </div>
        );
    }
}
export default ColorPanel;



