import React, { Component } from 'react';
import PropTypes from 'prop-types';
export default class TabContent extends Component {
    static propTypes = {
        handleChange: PropTypes.func,
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dwrui'
    };
    constructor(props) {
        super(props);
        //let value = this.resloveValue(props.value || props.defaultValue);
        this.state = {

        };
    }
    componentDidMount() {

    }
    componentWillReceiveProps(nextProps) {
        
    }
    componentDidUpdate() {
    }
    render() {
        const { sprefix, children, navKey, activeKey } = this.props;
        const cls = [`${ sprefix }-tab-content`];
        if(activeKey == navKey) {
            cls.push(`${ sprefix }-tab-content-active`);
        }
        return (
            <div className={ cls.join(' ') }>{ children }</div>
        );
    }
}


