import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
class DragDrop extends Component {
    static propTypes = {
        //onChange: PropTypes.func
    };
    static defaultProps = {
        sprefix: 'dwrui',
        //onChange: () => { }
    };
    constructor(props) {
        super(props);
        this.startPageX = 0;
        this.startPageY = 0;
        this.endPageX = 0;
        this.endPageY = 0;
        this.startX = 0;
        this.startY = 0;
        this.endX = 0;
        this.endY = 0;
        this.current = null;
        // let size = props.size || props.defaultSize;
        // size = size || {
        //     width: 100,
        //     height: 100
        // };
        // let zoom = props.zoom || props.defaultZoom;
        // zoom = zoom || 1;
        // this.intervals = [];
        // for(let i = .1; i < 1E5; i *= 10) {
        //     this.intervals.push(1 * i);
        //     this.intervals.push(2 * i);
        //     this.intervals.push(5 * i);
        // }
        let pos = props.pos || props.defaultPos || [0, 0];
        this.state = {
            pos
        }
    }
    componentDidMount() {
        const { handle } = this.props;
        if (handle) {
            this.handle = this.getDom(handle);
        } else {
            this.el = ReactDOM.findDOMNode(this);
            this.handle = this.el;
        }
        this.handle.addEventListener('mousedown', this.handleMouseDown, false);
    }
    componentWillReceiveProps(nextProps) {
        if ('pos' in nextProps) {
            const { pos } = nextProps;
            if (pos !== this.state.pos) {
                this.setState({
                    pos
                });
            }
        }
    }
    getDom(el) {
        if (el === undefined) {
            return null;
        }
        return el.get ? el.get(0) : (typeof el == "string" ? document.getElementById(el) : el);
    }
    getSpace() {
        if (this.props.getSpace) {
            return this.props.getSpace();
        }
        return 10;
    }
    getScale() {
        if (this.props.getScale) {
            return this.props.getScale();
        }
        return 1;
    }
    getPosByDom(dom) {
        let xy = dom.style.transform.split(/[(|,|)]/g);
        return {
            x: parseFloat(xy[1], 10),
            y: parseFloat(xy[2], 10)
        };
    }
    setPos(dom, pos) {
        dom.style.transform = `translate(${pos[0]}px, ${pos[1]}px)`;
        //dom.style.left = pos[0] + 'px';
        //dom.style.top = pos[1] + 'px';
    }
    getRect(dom) {
        let { x, y } = this.getPosByDom(dom);
        return {
            x,
            y,
            width: dom.offsetWidth,
            height: dom.offsetHeight
        };
    }
    // moveTo(pos) {
    //     this.setPos(this.el, pos);
    // }
    // showDrag() {
    //     this.drag.style.visibility = "visible";
    // }
    // hideDrag() {
    //     this.drag.style.visibility = "hidden";
    // }
    lock() {
        this.locked = true;
    }
    unlock() {
        this.locked = false;
    }
    isLocked() {
        return this.locked;
    }
    handleMouseDown = (e) => {
        if (e.preventDefault) {
            e.preventDefault();
        } else {
            e.returnValue = false;
        }
        if (e.stopPropagation) {
            e.stopPropagation();
        } else {
            e.cancelBubble = true;
        }
        if (e.button != 0 && e.button != 1) {
            return;
        }
        this.current = e.target;
        let { x, y } = this.getPosByDom(this.el);
        this.startPageX = this.endPageX = x;
        this.startPageY = this.endPageY = y;
        this.startX = e.pageX;
        this.startY = e.pageY;
        //this.increaseX = this.startX - this.startPageX;
        //this.increaseY = this.startY - this.startPageY;
        this.increaseXY = [0, 0];
        this.unlock();
        document.addEventListener('mousemove', this.handleMouseMove, false);
        document.addEventListener('mouseup', this.handleMouseUp, false);
        this.onMouseDown(e);
    }
    handleMouseMove = (e) => {
        if (e.preventDefault) {
            e.preventDefault();
        } else {
            e.returnValue = false;
        }
        if (e.stopPropagation) {
            e.stopPropagation();
        } else {
            e.cancelBubble = true;
        }
        if (this.isLocked()) {
            return;
        }
        this.endX = e.pageX;
        this.endY = e.pageY;
        if (this.endX - this.startX === 0 && this.endY - this.startY === 0) {
            return;
        }
        let scale = this.getScale();

        this.increaseXY = [Math.round((this.endX - this.startX) / this.getSpace() / scale) * this.getSpace(), Math.round((this.endY - this.startY) / this.getSpace() / scale) * this.getSpace()];

        this.endPageX = this.startPageX + this.increaseXY[0];
        this.endPageY = this.startPageY + this.increaseXY[1];

        //this.moveTo([this.endPageX, this.endPageY]);
        this.onMouseMove(e);
    }
    handleMouseUp = (e) => {
        if (e.preventDefault) {
            e.preventDefault();
        } else {
            e.returnValue = false;
        }
        if (e.stopPropagation) {
            e.stopPropagation();
        } else {
            e.cancelBubble = true;
        }
        this.lock();
        document.removeEventListener('mousemove', this.handleMouseMove);
        document.removeEventListener('mouseup', this.handleMouseUp);
        this.onMouseUp(e);
    }
    onMouseDown(e) {
        if (this.props.onMouseDown) {
            this.props.onMouseDown(e, this);
        }
    }
    onMouseMove(e) {
        if (!('pos' in this.props)) {
            this.setState({
                pos: [this.endPageX, this.endPageY]
            });
        }
        if (this.props.onMouseMove) {
            this.props.onMouseMove(e, this);
        }
    }
    onMouseUp(e) {
        if (this.props.onMouseUp) {
            this.props.onMouseUp(e, this);
        }
    }
    componentDidUpdate() {

    }
    componentWillUnmount() {
        this.handle.removeEventListener('mousedown', this.handleMouseDown, false);
    }
    render() {
        const { pos } = this.state;
        // const { sprefix } = this.props;
        let left = parseFloat(pos[0], 10) || 0;
        let top = parseFloat(pos[1], 10) || 0;
        const style = {
            ...this.props.children.props.style,
            transform: `translate(${ left }px, ${ top }px)`
        };
        // return React.cloneElement(this.props.children, {
        //     style
        // });
        return (
            // <div className={`${ sprefix }-dragDrop`} 
            //     ref={ ref => { this.el = ref; } } 
            //     style={{
            //         width: `${width}px`,
            //         height: `${height}px`,
            //         transform: `translate(${ left }px, ${ top }px)`
            //     }}
            // >{ this.props.children }</div>
            // <div className={`${ sprefix }-dragDrop`}>
                
                    React.cloneElement(React.Children.only(this.props.children), {
                        style
                    })
            // </div>
        );
    }
}
export default DragDrop;



