import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Tab from './Tab';
import TabContent from './TabContent';
export default class DynamicTab extends Component {
    static propTypes = {
        onChange: PropTypes.func
    };
    static defaultProps = {
        renderNav: () => {
            return null;
        },
        renderContent: () => {
            return null;
        }
    };
    constructor(props) {
        super(props);
        this.newTabIndex = 0;
        let value = props.defaultValue || [];
        if ('value' in props) {
            value = props.value || [];
        }
        let activeKey = props.defaultActiveKey || [];
        if ('activeKey' in props) {
            activeKey = props.activeKey || [];
        }
        this.state = {
            activeKey,
            value,
        };
    }

    onChange = activeKey => {
        this.setState({
            activeKey
        });
    };

    add = () => {
        const { value } = this.state;
        const activeKey = `newTab${ this.newTabIndex++ }`;
        value.push({ 
            title: this.props.renderNav(), 
            key: activeKey,
            content: this.props.renderContent(), 
        });
        this.setState({
            value,
            activeKey
        });
        if(this.props.onChange) {
            this.props.onChange(null, { ...this.props, value });
        }
    };

    remove = targetKey => {
        let { activeKey } = this.state;
        let lastIndex;
        this.state.value.forEach((item, i) => {
            if (item.key == targetKey) {
                lastIndex = i - 1;
            }
        });
        const value = this.state.value.filter(item => item.key !== targetKey);
        if (value.length && activeKey == targetKey) {
            if (lastIndex >= 0) {
                activeKey = value[lastIndex].key;
            } else {
                activeKey = value[0].key;
            }
        }
        this.setState({
            value,
            activeKey
        });
        if(this.props.onChange) {
            this.props.onChange(null, { ...this.props, value });
        }
    };

    render() {
        return (
            <Tab
                onChange={ this.onChange }
                activeKey={ this.state.activeKey }
                onAdd={ this.add }
                onRemove={ this.remove }
            >
                {
                    this.state.value.map(item => (
                        <TabContent
                            title={ item.title }
                            key={ item.key }
                            activeKey={ this.state.activeKey }
                            closable={ item.closable }
                        >
                            { item.content }
                        </TabContent>
                    ))
                }
            </Tab>
        );
    }
}


